import React, {Component, PropTypes} from 'react';
import {connect} from 'react-redux';
import {Field, reduxForm} from 'redux-form';
import {classes} from 'helpers/utils';
import {renderBidInput, renderGlobalError, renderInput, renderSelect} from '../components/field/field';
import {renderCurrency} from 'components/priceComp';
import validate from '../validate';
import _ from 'helpers/lodash';

class LotCalculatorForm extends Component {
  render() {
    const {
      locationLabel,
      enterBidLabel,
      calculateButtonText,
      calculateProgressButtonText,
      locationInputDefaultText,
      auction,
      lot,
      locations,
      handleSubmit,
      handleBlur,
      submitting,
      startingPriceItem
    } = this.props;

    const initialLocation = {
      key: '', value: locationInputDefaultText
    };

    const isShop = _.get(lot, 'status.isShop', false);

    const selectLocations = locations.map((location) => location.content)
      .filter(l => l.businessEntityCode === (isShop ? lot.businessEntity : auction.businessEntity))
      .reduce((curr, value) => {
        curr.push({key: value.locationKey, value: value.title});
        return curr;
      }, [initialLocation]);
    const formClassName = classes({
      'lot-calculator__form': true,
      'form-submitting': submitting
    });

    const calculateText = submitting ? calculateProgressButtonText : calculateButtonText;

    return (
      <form onSubmit={handleSubmit} className={formClassName} onBlur={handleBlur}>
        <div className="sm-col md-col-4 sm-col-12">
          <div className="clearfix">
            <div className="sm-col md-col-12 sm-col-12">
              <label>
                <span>{locationLabel}</span>
                <Field name="locationKey" component={renderSelect} props={{...this.props, selectOptions: selectLocations}} />
              </label>
            </div>
          </div>
        </div>
        <div className="sm-col md-col-8 sm-col-12">
          <div className="clearfix">
            <div className="sm-col md-col-7 sm-col-12">
              <label>
                <span>{`${enterBidLabel} ${renderCurrency(startingPriceItem.currency)}`}</span>
                <Field name="amount" component={isShop ? renderInput : renderBidInput} min={_.get(startingPriceItem, 'value')} props={this.props} />
              </label>
            </div>

            <div className="sm-col md-col-5 sm-col-12">
              <button className="button-element lot-calculator__button secondary-button button-submit"
                      disabled={submitting} type="submit">{calculateText}</button>
              <div className="global-error">{renderGlobalError(this.props)}</div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

LotCalculatorForm.propTypes = {
  handleSubmit: React.PropTypes.func,
  handleBlur: React.PropTypes.func,
  extras: PropTypes.object,
  error: PropTypes.object,
  auction: PropTypes.object,
  lot: PropTypes.object,
  startingPriceItem: PropTypes.object,
  locations: PropTypes.array,
  locationLabel: PropTypes.string,
  generalNotesText: PropTypes.string,
  enterBidLabel: PropTypes.string,
  calculateButtonText: PropTypes.string,
  calculateProgressButtonText: PropTypes.string,
  locationInputDefaultText: PropTypes.string,
  requiredFieldError: React.PropTypes.string,
  isValidNumberError: React.PropTypes.string,
  maxBidError: React.PropTypes.string,
  priceCalculatorPostError: React.PropTypes.string,
  submitting: React.PropTypes.bool
};

LotCalculatorForm.defaultProps = {
  submitting: false,
  auction: {},
  lot: {},
  locations: [],
  extras: {}
};

function getByPath(path) {
  return function (props) {
    return _.get(props, path);
  };
}

module.exports = {
  AuctionLotCalculatorForm: connect()(
    reduxForm({
      form: 'LotCalculatorForm',
      enableReinitialize: true,
      validate: validate.createValidator({
        locationKey: [validate.required({msg: 'requiredFieldError'})],
        amount: [validate.required({msg: 'requiredFieldError'}),
          validate.isValidInteger({msg: 'isValidNumberError'}),
          validate.greaterThanWithProps({msg: 'lessThanStartingBidError'}, getByPath('startingPriceItem.value')),
          validate.lessThan(100000000)({msg: 'maxBidError'})]
      })
    })(LotCalculatorForm)),

  ShopLotCalculatorForm: connect()(
    reduxForm({
      form: 'ShopLotCalculatorForm',
      enableReinitialize: true,
      validate: validate.createValidator({
        locationKey: [validate.required({msg: 'requiredFieldError'})],
        amount: [validate.required({msg: 'requiredFieldError'}),
          validate.isValidInteger({msg: 'isValidNumberError'}),
          validate.greaterThan(0)({msg: 'lessThanStartingBidError'}),
          validate.lessThanWithProps({msg: 'maxBidError'}, getByPath('startingPriceItem.value'))
        ]
      })
    })(LotCalculatorForm))
};



