import React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as ShopLotActions from 'ui/actions/ShopLotActions';
import * as WindowActions from 'ui/actions/WindowActions';
import {
  STATE_PLACE_OFFER,
  STATE_INCREASE_OFFER,
  STATE_PLACE_OFFER_EXPANDED,
  STATE_SOLD,
  STATE_IN_SHOPPING_CART
} from 'ui/actions/ShopLotActions';
import IncreaseOffer from './IncreaseOffer';
import PlaceOffer from './PlaceOffer';
import _ from 'helpers/lodash';
import './counter-offer.scss';


export class CounterOfferComponent extends React.Component {
  shouldComponentUpdate(nextProps) {
    return _.get(nextProps, 'lot.lotNumber', -1) > 0;
  }

  render() {
    if (this.shouldHide() || typeof window === 'undefined' || !window.isInternalUser) return null;

    const section = this.getRenderedSection();

    return (
      <div className="counter-offer">
        {section}
      </div>
    );
  }

  getRenderedSection() {
    const states = {
      [STATE_PLACE_OFFER]: () => <PlaceOffer {...this.props} />,
      [STATE_PLACE_OFFER_EXPANDED]: () => <PlaceOffer {...this.props} isExpanded />,
      [STATE_INCREASE_OFFER]: () => <IncreaseOffer {...this.props} />
    };

    const component = states[this.props.shopLot.state];
    if (component) return component();
  }

  shouldHide() {
    return [STATE_SOLD, STATE_IN_SHOPPING_CART].indexOf(this.props.shopLot.state) !== -1;
  }
}

CounterOfferComponent.propTypes = {
  lot: React.PropTypes.object,
  extras: React.PropTypes.object,
  shopLotActions: React.PropTypes.object,
  shopLot: React.PropTypes.object,
  authUrl: React.PropTypes.string
};

function mapStateToProps(state) {
  return {
    authUrl: state.config.authUrl,
    shopLot: state.shopLot,
    lot: _.get(state, 'modules.data.lot', {})
  };
}

function mapDispatchToProps(dispatch) {
  return {
    shopLotActions: bindActionCreators(ShopLotActions, dispatch),
    windowActions: bindActionCreators(WindowActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(CounterOfferComponent);



