import React, {PropTypes} from 'react';
import LotCostsLineComp from './LotCostsLineComp';
import MarkdownElement from 'components/markdown/MarkdownElement';
import _ from 'helpers/lodash';

const LotCostsComponent = (props) => {
  const {
    basePrice,
    resaleRights,
    buyersPremium,
    shippingCharges,
    shippingInsurance,
    purchasePrice,
    total
   } = props.costs;

  const {
    basePriceText,
    resaleRightsText,
    buyersPremiumText,
    shippingInsuranceText,
    shippingChargesText,
    totalText,
    priceNotesText,
    currency,
    lot,
    localPickup
  } = props;

  const isShop = _.get(lot, 'status.isShop', false);
  const buyersPremiumComp = isShop ? null : <LotCostsLineComp className="cost_calculator__buyers-premium" price={buyersPremium} priceText={buyersPremiumText} currency={currency} extras={props.extras} />;
  const resaleRightsComp = (lot.resaleRights && !isShop) ? (<LotCostsLineComp className="cost_calculator__resale-rights" price={resaleRights} priceText={resaleRightsText} currency={currency} extras={props.extras} />) : null;
  const shippingInsuranceComp = (localPickup) ? null :
    (<LotCostsLineComp
      className="cost_calculator__shipping-insurance"
      price={shippingInsurance}
      priceText={shippingInsuranceText}
      currency={currency}
      extras={props.extras} />);
  const shippingChargesComp = (localPickup) ? null:
    (<LotCostsLineComp
      className="cost_calculator__shipping-charges"
      price={shippingCharges}
      priceText={shippingChargesText}
      currency={currency}
      extras={props.extras} />);

  return (
    <div>
      <LotCostsLineComp className="cost_calculator__base-price" price={isShop ? purchasePrice : basePrice} priceText={basePriceText} currency={currency} extras={props.extras} />
      {buyersPremiumComp}
      {resaleRightsComp}
      {shippingInsuranceComp}
      {shippingChargesComp}
      <LotCostsLineComp price={total} priceText={totalText} currency={currency} extras={props.extras} className="lot-calculator__cost-total" />
      <div className="lot-calculator__cost-notes">
        <MarkdownElement className="sm-col md-col-12 sm-col-12" text={priceNotesText} />
      </div>
    </div>
  );
};

LotCostsComponent.propTypes = {
  basePriceText: PropTypes.string,
  resaleRightsText: PropTypes.string,
  buyersPremiumText: PropTypes.string,
  shippingChargesText: PropTypes.string,
  shippingInsuranceText: PropTypes.string,
  totalText: PropTypes.string,
  priceNotesText: PropTypes.string,
  lot: PropTypes.object,
  extras: PropTypes.object,
  costs: PropTypes.object,
  currency: PropTypes.string,
  localPickup: PropTypes.bool
};

LotCostsComponent.defaultProps = {
  basePriceText: '',
  resaleRightsText: '',
  buyersPremiumText: '',
  shippingChargesText: '',
  shippingInsuranceText: '',
  totalText: 'Total cost',
  priceNotesText: '',
  lot: {},
  extras: {},
  costs: {},
  currency: ''
};

export default LotCostsComponent;



