import React, {PropTypes} from 'react';
import PriceComp, {renderCurrency} from 'components/priceComp';

const LotCostsLineComp = (props) => {
  const { price, extras, priceText, currency, className } = props;
  let priceElem = <PriceComp {...price} extras={extras} decimalPlaces={2} />;
  if (isNaN(price.value)) {
    const priceFormat = (extras && extras.locale === 'de') ? `-- ${renderCurrency(currency)}` : `${renderCurrency(currency)} --`;
    priceElem = <span dangerouslySetInnerHTML={{__html: priceFormat}} />;
  }

  const componentClasses = `lot-calculator__cost-line ${className} sm-col md-col-12 sm-col-12 clearfix`;

  return (<div className={componentClasses}>
    <div className="left">{priceText}</div>
    <div className="right">
      {priceElem}
    </div>
    </div>
  );
};

LotCostsLineComp.propTypes = {
  className: PropTypes.string,
  priceText: PropTypes.string,
  currency: PropTypes.string,
  extras: PropTypes.object,
  price: PropTypes.object
};

LotCostsLineComp.defaultProps = {
  className: '',
  priceText: '',
  currency: '',
  extras: {},
  price: {}
};

export default LotCostsLineComp;



