var validate = require('../validate.js');

module.exports = validate.createValidator({
  Salutation: [validate.required({msg: 'requiredFieldError'})],
  FirstName: [validate.required({msg: 'requiredFieldError'})],
  LastName: [validate.required({msg: 'requiredFieldError'})],
  Email: [
    validate.required({msg: 'requiredFieldError'}),
    validate.isValidEmail({msg: 'isValidEmailFieldError'})
  ],
  PhoneNumber: [
    validate.required({msg: 'requiredFieldError'}),
    validate.isValidPhoneNumber({msg: 'invalidPhoneNumber'})
  ],
  contacttype: [validate.required({msg: 'requiredFieldError'})],
  Question: [validate.required({msg: 'requiredFieldError'})]
});



