import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import MarkdownElement from 'components/markdown/MarkdownElement';
import {buildAuctionLink} from 'helpers/utils';
import LinkElement from 'components/link-element/LinkElement';
import './lot-repeat-cta.scss';

class LotRepeatCta extends React.Component {
  render = () => {
    const {header, data: {auction: {workflowStageName}}} = this.props;

    if (workflowStageName && workflowStageName.toLowerCase() !== 'preview') {
      return null;
    }
    return (
      <div className="lot-repeat-cta__container section-wrapper px1 clearfix">
        <div className="lot-repeat-cta__section md-col md-col-2" />
        <div className="lot-repeat-cta__body md-col md-col-8">
          <div className="md-col sm-col-12 md-col-12">
            <div className="lot-repeat-cta__header">
              <h2>{header}</h2>
            </div>
            <div className="lot-repeat-cta__containers">
              {this.renderLeftSection()}
              {this.renderRightSection()}
            </div>
          </div>

        </div>
        <div className="lot-repeat-cta__section md-col md-col-2" />
      </div>
    );

  };

  /**
   * Function for rendering left CTA block
   * @returns {XML} Rendered dom with corresponding block
   */
  renderLeftSection = () => {
    const {leftSectionHeader, leftSectionBullets, leftSectionChecks, leftSectionButtonText, data, extras} = this.props;
    const registrationUrl = `${buildAuctionLink(data.auction, extras)}/registration`;

    return this.buildBlock('lot-repeat-cta__left-section',
      leftSectionHeader, leftSectionBullets, leftSectionChecks, leftSectionButtonText, registrationUrl, false);
  };

  /**
   * Function for rendering right CTA block
   * @returns {XML} Rendered dom with corresponding block
   */
  renderRightSection = () => {
    const {rightSectionHeader, rightSectionBullets, rightSectionChecks, rightSectionButtonText, rightSectionButtonUrl, data, extras} = this.props;
    const url = rightSectionButtonUrl.replace('{lotNumber}', data.lot.lotNumber);
    return this.buildBlock('lot-repeat-cta__right-section',
      rightSectionHeader, rightSectionBullets, rightSectionChecks, rightSectionButtonText, url, true, extras);
  };

  /**
   * Build repeat CTA block
   * @param {String} className Name of class which should be applied to the block
   * @param {String} headerText Text of the header of the block
   * @param {String} bulletPoints Markup string for bullet points part of block
   * @param {String} checkmarks Markup string for check marks points part of block
   * @param {String} buttonText Text for button in the block
   * @param {String} buttonUrl Url which should be applied for button
   * @param {Boolean} isSpa show spa link
   * @param {Object} extras Extras object
   * @returns {XML} Rendered dom with corresponding block
   */
  buildBlock = (className, headerText, bulletPoints, checkmarks, buttonText, buttonUrl, isSpa, extras) => {

    const buttonSettings = isSpa ? {
      spaLink: extras.addLocaleToUrl(buttonUrl)
    } : {
      link: buttonUrl
    };

    return (<div className={className + ' sm-col sm-col-12 md-col-6'}>
      <div className="body">
        <div className="cta-header">
          <p>{headerText}</p>
        </div>
        <div className="cta-content">
          <div className="cta-bullet-points">
            <MarkdownElement text={bulletPoints} />
          </div>
          <div className="cta-check-marks">
            <MarkdownElement text={checkmarks} />
          </div>
        </div>
        <div className="cta-footer">
          <LinkElement
            title={buttonText}
            className="link link--with-icon link--inverse"
            label={buttonText}
            {...buttonSettings}
            extras={extras}
          />
        </div>
      </div>
    </div>);
  }
}

LotRepeatCta.propTypes = {
  header: PropTypes.string,
  leftSectionButtonText: PropTypes.string,
  rightSectionButtonText: PropTypes.string,
  leftSectionBullets: PropTypes.string,
  rightSectionBullets: PropTypes.string,
  leftSectionChecks: PropTypes.string,
  rightSectionChecks: PropTypes.string,
  leftSectionHeader: PropTypes.string,
  rightSectionHeader: PropTypes.string,
  rightSectionButtonUrl: PropTypes.string,
  data: PropTypes.object,
  extras: PropTypes.object
};

function mapStateToProps(state) {
  return {
    data: state.modules.data
  };
}

export default connect(mapStateToProps)(LotRepeatCta);



