import {
  postRequest,
  buildHeaderObject
} from 'helpers/rest';
const EMAIL_VERIFICATION_POST_URL = '/_/verifyEmail';

/**
 * Validate email with notification-service
 * WARNING: it validates only Email field in upppercase
 * @param  {[type]} errorMessage [description]
 * @return {[type]}              [description]
 */
module.exports = function emailValidatorFactory(errorMessage) {
  return (values) => {
    return postRequest(EMAIL_VERIFICATION_POST_URL,
        JSON.stringify(values),
        buildHeaderObject(), 3000
      )
      .then((res) => {
        if (res.status === 417) {
          throw {
            Email: [errorMessage]
          };
        }
      }).catch((err) => {
        if (err.status === 417) {
          throw {
            Email: [errorMessage]
          };
        }
      });
  };
};



