import React from 'react';
import LinkComp from 'components/linkComp';
import _ from 'helpers/lodash';

export const Sold = (props) => {
  let findSimilarItemsLink = <LinkComp className="link link--with-icon shop-lot-teaser__find-similar"
                                       link={_.get(props, 'lot.status.location')}
                                       extras={props.extras}>{props.findSimilarItemsLinkText}</LinkComp>;
  return (
    <div>
      <div className="shop-lot-teaser__sold-message">{props.soldMessageText}</div>
      <div className="shop-lot-teaser__buy-now-text">{props.soldDescription}</div>
      {findSimilarItemsLink}
    </div>

  );
};

Sold.propTypes = {
  lot: React.PropTypes.object,
  extras: React.PropTypes.object,
  soldPriceText: React.PropTypes.string,
  soldMessageText: React.PropTypes.string,
  soldDescription: React.PropTypes.string,
  findSimilarItemsLinkText: React.PropTypes.string
};

export default Sold;



