import React from 'react';
import LinkComp from 'components/linkComp';
import _ from 'helpers/lodash';

import './lots-navigation.scss';

/**
 * Lot Navigation Next and Previous.
 */
export default class LotsNavigation extends React.Component {
  constructor(props) {
    super(props);
    this.currentIndex = -1;
  }

  render() {
    const link = this.getLink();
    const prev = link.prev();
    const next = link.next();
    const goToPreviousLot = `${this.props.goToPreviousLot} #${prev.catalogNumber} | ${prev.title}`;
    const goToNextLot = `${this.props.goToNextLot} #${next.catalogNumber} | ${next.title}`;

    const prevLink = (
      <LinkComp spaLink={prev.link} extras={this.props.extras}
                className="link link--with-icon link--back" onClick={this.updateIndex} title={goToPreviousLot} />
    );

    const nextLink = (
      <LinkComp spaLink={next.link} extras={this.props.extras}
                className="link link--with-icon" onClick={this.updateIndex} title={goToNextLot} />
    );

    /**
     * Flag to indicate, if navigation to lot was made by
     * navigation buttons or with back/forward browser buttons
     * @type {boolean}
     */
    this.isNavButtonsUsed = false;

    return (
      <div className="lots-navigation">
        {prev.link ? prevLink : null}
        <span>{this.props.item}</span>
        {next.link ? nextLink : null}
      </div>
    );
  }

  /**
   * Get current lot index from an lots props array
   * @returns {number} current index
   */
  getCurrentIndex() {
    if (!this.isNavButtonsUsed || this.currentIndex === -1) {
      this.currentIndex = _.findIndex(this.props.auction.lots, {
        catalogNumber: this.props.lot.catalogNumber
      });
    }

    return this.currentIndex;
  }

  /**
   * Get next and previous functions. If link is unavailable (e.g. it's first lot and back id not available) -
   *  empty object is returned from functions.
   * @returns {{next: (function()), prev: (function())}}
   */
  getLink() {
    const { extras: { addLocaleToUrl } } = this.props;
    const index = this.getCurrentIndex();
    const lots = this.props.auction.lots;

    const action = (diff) => {
      const lotData = _.get(lots, `[${index + diff}]`);

      if (lotData) {
        lotData.link = addLocaleToUrl(`/o/${lotData.lotNumber}/${lotData.slug}`);
      }

      return lotData || {};
    };

    return {
      next: () => {
        return action(1);
      },
      prev: () => {
        return action(-1);
      }
    };
  }

  /**
   * Update current lot index.
   * @param {object} e event
   */
  updateIndex = (e) => {
    e.stopPropagation();

    let diff = 1;

    if (e.target.classList.contains('link--back')) {
      diff = -1;
    }
    this.isNavButtonsUsed = true;
    this.currentIndex += diff;
  };

  shouldComponentUpdate(nextProps) {
    return nextProps.extras.locale !== this.props.extras.locale ||
           nextProps.lot.lotNumber !== this.props.lot.lotNumber;
  }
}

LotsNavigation.propTypes = {
  extras: React.PropTypes.object,
  lot: React.PropTypes.object,
  auction: React.PropTypes.object,
  item: React.PropTypes.string,
  goToPreviousLot: React.PropTypes.string,
  goToNextLot: React.PropTypes.string,
};

LotsNavigation.defaultProps = {
  extras: {},
  auction: {},
  lot: {},
  item: '',
  goToPreviousLot: '',
  goToNextLot: ''
};



