import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as LotActions from 'ui/actions/LotActions';
import {classes} from '../helpers/utils';
import _ from 'helpers/lodash';
import LinkElement from 'components/link-element/LinkElement';

export class LotWishlistContainer extends React.Component {
  componentDidMount() {
    this.checkLotInWishList(this.props);
  }

  componentWillReceiveProps(nextProps) {
    if(this.props.lotNumber !== nextProps.lotNumber) {
      this.checkLotInWishList(nextProps);
    }
  }

  /**
   * Check if lot is bookmarked for current user
   * @returns {Promise} Promise which checks the bookmark
   */
  checkLotInWishList = (props) => {
    const { config: { authUrl } } = props;
    return this.props.actions.checkLotInWishlist({
      lotNumber: props.lotNumber
    }, authUrl);
  };

  toggleLotInWishlist = () => {
    const {config, wishlist} = this.props;

    return this.props.actions.toggleLotInWishlist({
      config: config,
      bookmarked: wishlist.bookmarked,
      lotNumber: this.props.lotNumber
    }, config.authUrl);
  };

  render() {
    const bookmarked = this.props.wishlist.bookmarked;
    const label = this.props.wishlist.bookmarked ? this.props.savedLabel : this.props.unsavedLabel;
    const classesForBody = classes({
      'link': true,
      'link--with-icon': true,
      'link--wishlist': true,
      'unsaved': !bookmarked,
      'saved': bookmarked
    });

    return (
        <LinkElement title={this.props.tooltip} className={classesForBody} label={label}
                     onClick={_.debounce(this.toggleLotInWishlist, 300)} />
    );
  }
}

function mapStateToProps(state) {
  return {
    wishlist: state.wishlist,
    config: state.config
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(LotActions, dispatch)
  };
}
LotWishlistContainer.propTypes = {
  actions: PropTypes.object,
  wishlist: PropTypes.object,
  config: PropTypes.object,
  lotNumber: PropTypes.number,
  savedLabel: PropTypes.string,
  unsavedLabel: PropTypes.string,
  tooltip: PropTypes.string
};

LotWishlistContainer.defaultProps = {
  savedLabel: 'Saved',
  unsavedLabel: 'Wish list'
};

export default connect(mapStateToProps, mapDispatchToProps)(LotWishlistContainer);



