import React, {  PropTypes, Component } from 'react';
import Tooltip from 'components/tooltip/Tooltip';

import './watching-counter.scss';

export class WatchingCounter extends Component {
  componentDidMount() {
    return this.props.lotActions.getWatchingCount(this.props.lot.lotNumber);
  }

  render() {
    const {watchingCounterTooltip, shopLot} = this.props;

    if (!shopLot.watching) return null;

    return (
      <span className="watching-counter__container">
        <Tooltip placement="bottom" overlay={watchingCounterTooltip}>
          <div className="watching-counter__icon">{shopLot.watching}</div>
        </Tooltip>
      </span>
    );
  }
}

WatchingCounter.propTypes = {
  shopLot: PropTypes.object,
  data: PropTypes.object,
  lot: PropTypes.object,
  lotActions: PropTypes.object,
  extras: PropTypes.object,
  watchingCounterTooltip: PropTypes.string
};

WatchingCounter.defaultProps = {
  watchingCounterTooltip: ''
};

export default WatchingCounter;



