import {connect} from 'react-redux';
import React from 'react';
import {Field, reduxForm} from 'redux-form';
import './lot-question-form.scss';
import { classes } from 'helpers/utils';
import validate from './lotQuestionValidator';
import trueEmailValidator from './trueEmailValidator';
import {renderInput, renderTextarea, renderCheckbox, renderGlobalError, renderSelect} from '../components/field/field';

const LotQuestionForm = (props) => {
  const {
      handleSubmit,
      submitting,
      asyncValidating,
      salutationLabelText,
      firstNameLabelText,
      lastNameLabelText,
      phoneNumberLabelText,
      emailLabelText,
      questionLabelText,
      mrSalutationText,
      mrsSalutationText,
      contactPrefText,
      phoneText,
      emailText,
      sendButtonText,
      newsletterLabelText,
      userLoggedIn,
      receiveNewsletter,
      plsSelectText
    } = props;
  const salutations = [
    {
      key: '',
      value: plsSelectText
    },
    {
      key: 'Herr',
      value: mrSalutationText
    },
    {
      key: 'Frau',
      value: mrsSalutationText
    }
  ];
  const preferences = [
    {
      key: 'Email',
      value: emailText
    },
    {
      key: 'PhoneNumber',
      value: phoneText
    }
  ];
  const newsletterCheckbox = (!userLoggedIn || !receiveNewsletter) && (
      <div className="sm-col sm-col-12">
        <Field name="ReceiveNewsletters" props={props} component={renderCheckbox(newsletterLabelText)} />
      </div>
    );
  const formClassName = classes({
    'lot-question-form': true,
    'form-submitting': submitting || asyncValidating
  });

  return (
    <form onSubmit={handleSubmit} className={formClassName}>
      <div className="sm-col md-col-6 sm-col-12">
        <div className="clearfix">
          <div className="sm-col md-col-6 sm-col-12">
            <label>
              <span>{salutationLabelText}</span>
              <Field name="Salutation" props={{ ...props, selectOptions: salutations}} component={renderSelect} defaultValue="" />
            </label>
          </div>
        </div>
        <div className="clearfix">
          <div className="sm-col md-col-6 sm-col-12">
            <label>
              <span>{firstNameLabelText}</span>
              <Field name="FirstName" props={props} component={renderInput} />
            </label>
          </div>
          <div className="sm-col md-col-6 sm-col-12">
            <label>
              <span>{lastNameLabelText}</span>
              <Field name="LastName" props={props} component={renderInput} />
            </label>
          </div>
        </div>
        <div className="sm-col sm-col-12">
          <label>
            <span>{emailLabelText}</span>
            <Field name="Email" type="email" props={props} component={renderInput} />
          </label>
        </div>
        <div className="sm-col md-col-6 sm-col-12">
          <label>
            <span>{phoneNumberLabelText}</span>
            <Field name="PhoneNumber" type="tel" props={props} component={renderInput} />
          </label>
        </div>
        <div className="sm-col md-col-6 sm-col-12">
          <label>
            <span>{contactPrefText}</span>
            <Field name="contacttype" props={{ ...props, selectOptions: preferences}} component={renderSelect} defaultValue="tel" />
          </label>
        </div>
        {newsletterCheckbox}
      </div>
      <div className="sm-col md-col-6 sm-col-12">
        <div className="question-field">
          <label>
            <span>{questionLabelText}</span>
            <Field name="Question" props={props} component={renderTextarea} />
          </label>
        </div>
        <div className="global-error sm-col md-col-8 sm-col-12">{renderGlobalError(props)}</div>
        <div className="lot-question-form__send sm-col md-col-4 sm-col-12">

          <button className="button-element primary-button button-submit" type="submit"
                  disabled={submitting}>{sendButtonText}</button>
        </div>

      </div>
    </form>
  );
};

LotQuestionForm.propTypes = {
  handleSubmit: React.PropTypes.func,
  error: React.PropTypes.object,
  pristine: React.PropTypes.bool,
  touched: React.PropTypes.bool,
  userLoggedIn: React.PropTypes.bool,
  receiveNewsletter: React.PropTypes.bool,
  reset: React.PropTypes.func,
  submitting: React.PropTypes.bool,
  asyncValidating: React.PropTypes.bool,
  firstNameLabelText: React.PropTypes.string,
  lastNameLabelText: React.PropTypes.string,
  phoneNumberLabelText: React.PropTypes.string,
  emailLabelText: React.PropTypes.string,
  questionLabelText: React.PropTypes.string,
  salutationLabelText: React.PropTypes.string,
  mrSalutationText: React.PropTypes.string,
  mrsSalutationText: React.PropTypes.string,
  contactPrefText: React.PropTypes.string,
  phoneText: React.PropTypes.string,
  emailText: React.PropTypes.string,
  sendButtonText: React.PropTypes.string,
  newsletterLabelText: React.PropTypes.string,
  requiredFieldError: React.PropTypes.string,
  invalidPhoneNumber: React.PropTypes.string,
  isValidEmailFieldError: React.PropTypes.string,
  isTrueEmailFieldError: React.PropTypes.string,
  plsSelectText: React.PropTypes.string,
  lotQuestionPostError: React.PropTypes.string
};

LotQuestionForm.defaultProps = {
  firstNameLabelText: '',
  lastNameLabelText: '',
  phoneNumberLabelText: '',
  emailLabelText: '',
  questionLabelText: '',
  requiredFieldError: '',
  isValidEmailFieldError: '',
  isTrueEmailFieldError: '',
  lotQuestionPostError: '',
  salutationLabelText: '',
  mrSalutationText: '',
  mrsSalutationText: '',
  plsSelectText: '',
  contactPrefText: '',
  phoneText: '',
  emailText: '',
  sendButtonText: '',
  newsletterLabelText: ''
};

function mapStateToProps(state) {

  if (state.user.unknown) {
    return {
      userLoggedIn: !!state.user.email
    };
  }

  if (state.user.country && !!state.user.email && state.user.profile.FirstName) {

    return {
      userLoggedIn: !!state.user.email,
      initialValues: {
        contacttype: 'email',
        Email: state.user.email,
        Salutation: state.user.profile.Salutation,
        FirstName: state.user.profile.FirstName,
        LastName: state.user.profile.LastName,
        PhoneNumber: state.user.profile.Phone,
        ReceiveNewsletters: state.user.country !== 'DE'
      },
      receiveNewsletter: state.user.profile.ReceiveNewsletter
    };
  }
  if (state.user.country && !state.user.email) {
    return {
      userLoggedIn: !!state.user.email,
      initialValues: {
        contacttype: 'email',
        ReceiveNewsletters: state.user.country !== 'DE'
      }
    };
  }
  return {
    userLoggedIn: !!state.user.email
  };
}

export default connect(mapStateToProps)(reduxForm({
  form: 'LotQuestionForm',
  validate,
  asyncValidate: trueEmailValidator({msg: 'isTrueEmailFieldError'}),
  asyncBlurFields: ['Email']
})(LotQuestionForm));



