import React, {PropTypes} from 'react';
import ImageElement from 'components/images/ImageElement';
import {setImageSize} from 'helpers/utils.js';
import {connect} from 'react-redux';
import ReactDOM from 'react-dom';

class LightboxComponent extends React.Component {
  constructor() {
    super();
    this.state = {
      images: [],
      activeItem: -1
    };
    this.localExtras = {};
  }

  getNextIndex = (currentIndex, length) => {
    return ((currentIndex % length) + length) % length;
  };

  /**
   * Used for setting correct active image index
   * @param {number} diff Specifies index of movement forward or back
   */
  setActiveIndex = (diff) => {
    const currentIndex = this.getNextIndex(this.state.activeItem + diff, this.props.images.length);

    this.setState({
      activeItem: currentIndex
    });
  };

  /**
   * Moves to next slide
   */
  nextSlide = () => {
    this.setActiveIndex(1);
  };

  /**
   * Moves to prev slide
   */
  prevSlide = () => {
    this.setActiveIndex(-1);
  };

  /**
   * Closes lightbox
   */
  closeLightbox = () => {
    this.props.extras.onLightboxClosed();
  };

  /**
   * Build an appropriate Node tree for min slide viewer
   * @param {Array} images Array with all images which are used in gallery
   * @param {number} gallerySelectedIndex Image which was selected in gallery
   * @returns {Object} Image which should be displayed
   */
  prepareSlide = (images, gallerySelectedIndex) => {
    const {slideHeight, slideWidth} = this.props;
    const currentItem = images[gallerySelectedIndex];

    return <ImageElement imageUrl={setImageSize(slideHeight, slideWidth, currentItem.url)}
                         extras={this.localExtras} onLoad={this.positionLightbox} />;
  };

  /**
   * Toggles body class which is used for scroll disable
   * @param {boolean} isOpened Flag which is equal to lightbox open state
   */
  toggleBodyClass = (isOpened) => {
    if (isOpened) {
      document.body.classList.add('lightbox-open');
    }
    else {
      document.body.classList.remove('lightbox-open');
    }
  };

  /**
   * Renders left and right buttons for lightbox
   * @returns {Object} Tree with controls
   */
  renderControls = () => {
    if (this.props.images.length <= 1) {
      return null;
    }

    return (
      <div>
        <div className="lightbox-component__left-block">
          <span className="lightbox-component__prev-slide" onClick={this.prevSlide} />
        </div>
        <div className="lightbox-component__right-block">
          <span className="lightbox-component__next-slide" onClick={this.nextSlide} />
        </div>
      </div>
    );
  };

  /**
   * check if user clicked outside of lightbox (grey area)
   * @param {Object} e Event object
   */
  checkClick = (e) => {
    if (e.target.classList.contains('lightbox-component__body')) {
      this.closeLightbox();
    }
  };

  /**
   * Renders main lightbox component
   * @returns {XML}
   */
  render() {
    const {images, extras} = this.props;
    const currentIndex = this.state.activeItem === -1 ? extras.selectedItem : this.state.activeItem;

    this.localExtras = {
      ...extras,
      lazyLoading: false
    };

    const activeSlide = this.prepareSlide(images, currentIndex);
    this.toggleBodyClass(true);

    return <div className="">
      <div className="lightbox-component__overlay" onClick={this.closeLightbox} />
      <div className="lightbox-component__body" onClick={this.checkClick}>
        {this.renderControls()}
        <span className="content-height" />
        <div className="lightbox-component__slide-wrapper">
          <figure className="lightbox-component__slide-holder">
            {activeSlide}
            <div className="lightbox-component__slide-holder-footer">
              <div className="px1 sm-col sm-col-2 md-col-5">
                <div className="wrap lightbox-component__slide-holder-footer__caption">
                  <p>
                    <span>{this.props.extras.caption}</span>
                  </p>
                </div>
              </div>
              <div className="lightbox-component__slide-counter px1 sm-col sm-col-8 md-col-2">
                <div className="wrap">
                  <p>
                    <span>{currentIndex + 1}</span>
                    <span> / </span>
                    <span>{this.props.images.length}</span>
                  </p>
                </div>
              </div>
              <div className="px1 sm-col sm-col-2 md-col-5">
                <div className="lightbox-component__slide-close-block">
                  <span className="lightbox-component__close" onClick={this.closeLightbox} />
                </div>
              </div>
            </div>
          </figure>
        </div>
      </div>
    </div>;
  }

  /**
   * Controling lightbox by buttons on the keyboard
   * @param {Object} event Controls lightbox by pressing the appropriate arrow button or Esc
   */
  keypressHandler = (event) => {
    const handlers = {
      27: this.closeLightbox,
      39: this.nextSlide,
      37: this.prevSlide
    };

    return handlers[event.keyCode] ? handlers[event.keyCode]() : null;
  };

  componentWillMount() {
    this.setState({
      activeItem: this.props.extras.selectedItem
    });
  }

  componentDidMount() {
    document.addEventListener('keydown', this.keypressHandler);
    this.positionLightbox();
  }

  componentWillUnmount() {
    this.toggleBodyClass(false);
    document.removeEventListener('keydown', this.keypressHandler);
  }

  /**
   * Hack for correct vertical positioning of light box
   */
  positionLightbox = () => {
    const rootNode = ReactDOM.findDOMNode(this);
    const figureEl = rootNode.querySelector('.lightbox-component__slide-holder');
    const footerEl = rootNode.querySelector('.lightbox-component__slide-holder-footer');
    const imgEl = rootNode.querySelector('.lightbox-component__body figure img');
    const height = imgEl.offsetHeight + footerEl.offsetHeight;

    figureEl.style.top = ((window.innerHeight - height) / 2) - 20 + 'px';
    footerEl.style.maxWidth = imgEl.offsetWidth + 'px';
  };

  componentDidUpdate() {
    var rootNode = ReactDOM.findDOMNode(this);

    // Hack for correct footer width calculation, which force webkit reflow
    let el = rootNode.querySelector('div.lightbox-component__body img');

    el.style.maxWidth = '99%';
    const height = el.offsetHeight;
    el.style.maxWidth = '100%';
    this.positionLightbox(rootNode);

    return height;
  }
}

LightboxComponent.propTypes = {
  images: PropTypes.array,
  extras: PropTypes.object,
  windowResize: PropTypes.object,
  slideHeight: PropTypes.number,
  slideWidth: PropTypes.number
};

LightboxComponent.defaultProps = {
  images: [],
  extras: [],
  windowResize: {},
  slideHeight: 750,
  slideWidth: 1000
};

function mapStateToProps(state) {
  return {
    windowResize: state.windowResize,
    config: state.config
  };
}

export default connect(mapStateToProps)(LightboxComponent);



