import React from 'react';
import * as UserActions from 'ui/actions/UserActions';
import * as LotActions from 'ui/actions/LotActions';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import _ from 'helpers/lodash';
import PriceComp from 'components/priceComp';
import { auctionDateToString, buildAuctionLink } from 'helpers/utils';
import GalleryComponent from 'gallery/GalleryComponent';
import PriceTooltip from 'components/price-tooltip/PriceTooltip';
import ButtonElement from 'components/buttons/ButtonElement';
import CalendarButton from 'components/action-button-calendar/CalendarButton';
import LinkComp from 'components/linkComp';
import PrintButton from 'components/action-button-print/PrintButton';
import EmailButton from 'components/action-button-email/EmailButton';
import LotWishlistContainer from 'lot-wishlist/LotWishlistContainer';
import LinkElement from 'components/link-element/LinkElement';
import MarkdownElement from 'components/markdown/MarkdownElement';
import LotsNavigation from 'components/lot-navigation/LotsNavigation';

import './lot-teaser.scss';


export class LotTeaserComponent extends React.Component {
  constructor(props) {
    super(props);
  }

  componentWillReceiveProps(nextProps) {
    const wasLoggedIn = _.get(this.props, 'userLoggedIn');
    const isLoggedIn = _.get(nextProps, 'userLoggedIn');
    const auctionNumber = _.get(this.props, 'auction.auctionNumber');
    const nextAuctionNumber = _.get(nextProps, 'auction.auctionNumber');
    const lotNumber = _.get(this.props, 'lot.lotNumber');
    const nextLotNumber = _.get(nextProps, 'lot.lotNumber', -1);

    if ((isLoggedIn && !wasLoggedIn)
        || (auctionNumber !== nextAuctionNumber)) {
      this.props.actions.getAuctionData(nextAuctionNumber);
    }

    if (lotNumber !== nextLotNumber && nextLotNumber > 0) {
      this.props.lotActions.visitLotPage({
        lotNumber: nextLotNumber,
        token: _.get(this.props, 'config.authToken')
      });
    }
  }

  shouldComponentUpdate(nextProps) {
    const nextLotNumber = _.get(nextProps, 'lot.lotNumber', -1);
    return nextLotNumber > 0;
  }

  componentDidMount() {
    const { userLoggedIn, lot, auction } = this.props;
    if (userLoggedIn && auction.auctionNumber) {
      this.props.actions.getAuctionData(auction.auctionNumber);
    }

    if (lot.lotNumber > 0) {
      this.props.lotActions.visitLotPage({
        lotNumber: lot.lotNumber,
        token: _.get(this.props, 'config.authToken')
      });
    }
  }

  /**
   * Show back link.
   */
  renderBackLink() {
    const {extras, auction, lot, backToAuction} = this.props;
    const isShop = _.get(lot, 'status.isShop', false);
    let text = this.props.backToAuctionText;
    if (!extras.isMobile) {
      text = `${backToAuction} ${auction.title}`;
    }

    let linkProps = {
      spaLink: buildAuctionLink(auction, extras),
      extras: extras,
      className: 'link link--with-icon link--back'
    };

    if (isShop) {
      //temporary untill we will implement proper SPA link
      text = this.props.backToAuctionText;
      linkProps = {
        ...linkProps,
        link: '#',
        spaLink: null,
        onClick: function() {
          window.history.back();
          return false;
        }
      };
    }

    return (
      <div className="lot-teaser__back-to-auction">
        <LinkComp {...linkProps}>
          {text}
        </LinkComp>
      </div>
    );
  }

  /**
   * Return absentee bid URL.
   *
   * @returns {string}
   */
  getAbsenteeBidURL() {
    const {
      lot: {lotNumber},
      absenteeBidUrl,
      userLoggedIn,
      absenteeBidUrlForLoggedInUser,
      extras: { addLocaleToUrl }
    } = this.props;

    const urlTemplate = userLoggedIn ? absenteeBidUrlForLoggedInUser : absenteeBidUrl;

    return addLocaleToUrl(urlTemplate.replace('{lotNumber}', lotNumber));
  }

  renderWhitelistWarning() {
    const { whiteListWarningText } = this.props;
    return <div className="lot-teaser__white-list-warning">
      <MarkdownElement text={whiteListWarningText} />
    </div>;
  }

  render() {
    const {
      lot,
      auction,
      extras,
      startingPriceText,
      user,
      userLoggedIn,
      enterAbsenteeBidText,
      joinLiveAuctionText,
      registerAuctionText,
      auctionIsLiveText,
      auctionTakesPlaceText,
      lotQuestionHoverText,
      youAreRegisteredText,
      auctionLinkText,
      lotQuestionLink,
      rightsText
  } = this.props;
    const unknown = _.get(user, 'unknown');
    const locale = _.get(extras, 'locale', 'en');
    const catalogNumber = _.get(lot, 'catalogNumber', 0);
    const title = _.get(lot, 'title', 'NO TITLE');
    const startingPrice = _.get(lot, 'startingBid', 0);
    const isWhitelistedLot = _.get(lot, 'requiresWhitelistMapping', false);
    const auctionNumber = _.get(auction, 'auctionNumber', '');
    const externalNumber = _.get(auction, 'externalNumber', '');
    const isHandheld = _.get(extras, 'isHandheld');
    const isLive = _.get(auction, 'isLive') || _.get(auction, 'isOpen');
    const isRegisteredUserForAuction = _.get(user, ['auctions', auctionNumber, 'UserIsRegistered']);
    const auctionStartDate = _.get(auction, 'startDate', '');

    var primaryButton = <ButtonElement text={enterAbsenteeBidText} buttonType="primary" spaUrl={this.getAbsenteeBidURL()} extras={extras} />,
      registrationUrl = `${buildAuctionLink(auction, extras)}/registration`,
      registerAuctionLink = <LinkComp link={registrationUrl} extras={extras} className="link link--with-icon lot-teaser__registration"> {registerAuctionText}</LinkComp>,
      auctionIsLiveStatement = null,
      auctionInfo = (<div className="lot-teaser__auction-info">
        <div className="lot-teaser__auction-info-label">
          <LinkComp link={buildAuctionLink(auction, extras)} extras={extras} className="link">
            {auctionLinkText.replace('{auctionNumber}', externalNumber)}
          </LinkComp>
          <span className="lot-teaser__auction-info-take-place">{auctionTakesPlaceText}</span>
        </div>
        <div className="lot-teaser__auction-info-take-place-date">{auctionDateToString(auctionStartDate, locale)}</div>
      </div>);

    if (!unknown && userLoggedIn) {
      if (isRegisteredUserForAuction) {
        registerAuctionLink = <div className="lot-teaser__already-registered">{youAreRegisteredText}</div>;
      }
    }
    if (isLive) {
      if (!isRegisteredUserForAuction) {
        registerAuctionLink = null;
      }
      auctionInfo = null;
      auctionIsLiveStatement = <div className="lot-teaser__auction-is-live">{auctionIsLiveText}</div>;
      primaryButton = <ButtonElement text={joinLiveAuctionText} buttonType="primary" url={registrationUrl} extras={extras} />;
    }

    const rightsIndicator = lot.representedVgBild ? (<div className="lot-teaser__rights-text">{rightsText}</div>) : null;

    return (<div className="section-wrapper clearfix lot-teaser px1">
      <div className="col-12 clearfix">
        <div className="clearfix">
          <div className="left">
            {this.renderBackLink()}
          </div>
          <div className="right">
            <LotsNavigation {...this.props} />
          </div>
        </div>
        {rightsIndicator}
      </div>

      {isWhitelistedLot && this.renderWhitelistWarning()}
      <div className="sm-col-12 md-col-8 md-col">
        <GalleryComponent {...this.props} />
      </div>
      <div className="sm-col-12 md-col-4 md-col lot-teaser__description_wrapper">
        <h1 className="lot-teaser__title">
          <span className="lot-teaser__lot-number">{catalogNumber} |</span> {title}
        </h1>
        <div className="lot-teaser__starting-price">
          {startingPriceText}
          <PriceComp value={startingPrice.value} currency={startingPrice.currency} extras={extras} />
          <PriceTooltip {...this.props} />
        </div>
        {auctionIsLiveStatement}
        {primaryButton}
        {auctionInfo}
        {registerAuctionLink}

        <div className="lot-teaser__action-button">
          <CalendarButton extras={extras} auction={auction} title={this.props.calendarButtonTitle} />
          <EmailButton
            body={this.props.emailBody}
            subject={this.props.emailSubject}
            title={this.props.emailHoverTitle}
            dynamicTitle={this.props.dynamicTitle}
            userEmail={user.email}
          />
          {!isHandheld && <PrintButton title={this.props.printHoverTitle} />}
          <LinkElement link={lotQuestionLink} className="link link--action-button link--question"
                       title={lotQuestionHoverText}
          />
          <LotWishlistContainer lotNumber={lot.lotNumber}
                                savedLabel={this.props.lotWishlistSaved}
                                unsavedLabel={this.props.lotWishlistUnsaved}
                                tooltip={this.props.lotWishlistTooltip} />
        </div>
      </div>
    </div>);
  }
}

LotTeaserComponent.propTypes = {
  auction: React.PropTypes.object,
  lot: React.PropTypes.object,
  actions: React.PropTypes.object,
  extras: React.PropTypes.object,
  user: React.PropTypes.object,
  userLoggedIn: React.PropTypes.bool,
  ageRestrictionTooltip: React.PropTypes.string,
  priceDetailsInfo: React.PropTypes.string,
  startingPriceText: React.PropTypes.string,
  enterAbsenteeBidText: React.PropTypes.string,
  joinLiveAuctionText: React.PropTypes.string,
  registerAuctionText: React.PropTypes.string,
  youAreRegisteredText: React.PropTypes.string,
  auctionTakesPlaceText: React.PropTypes.string,
  auctionLinkText: React.PropTypes.string,
  auctionIsLiveText: React.PropTypes.string,
  backToAuction: React.PropTypes.string,
  backToAuctionText: React.PropTypes.string,
  lotActions: React.PropTypes.object,
  lotWishlistUnsaved: React.PropTypes.string,
  lotWishlistSaved: React.PropTypes.string,
  lotWishlistTooltip: React.PropTypes.string,
  rightsText: React.PropTypes.string,
  item: React.PropTypes.string,
  config: React.PropTypes.object,
  calendarButtonTitle: React.PropTypes.string,
  emailBody: React.PropTypes.string,
  emailSubject: React.PropTypes.string,
  emailHoverTitle: React.PropTypes.string,
  printHoverTitle: React.PropTypes.string,
  lotQuestionHoverText: React.PropTypes.string,
  lotQuestionLink: React.PropTypes.string,
  absenteeBidUrl: React.PropTypes.string,
  absenteeBidUrlForLoggedInUser: React.PropTypes.string,
  dynamicTitle: React.PropTypes.string,
  artistsLoyaltyTooltip: React.PropTypes.string,
  whiteListWarningText: React.PropTypes.string
};

LotTeaserComponent.defaultProps = {
  ageRestrictionTooltip: 'ageRestrictionTooltip',
  priceDetailsInfo: 'priceDetailsInfo',
  startingPriceText: 'Starting Price',
  joinLiveAuctionText: 'Join Auction!',
  enterAbsenteeBidText: 'Enter Absentee Bid',
  registerAuctionText: 'Register Auction',
  auctionIsLiveText: 'Auction is Live!',
  youAreRegisteredText: 'You are registered!',
  auctionTakesPlaceText: 'takes place on:',
  auctionLinkText: 'Auction No.{auctionNumber}',
  lotWishlistUnsaved: '',
  whiteListWarningText: '',
  lotWishlistSaved: '',
  lotWishlistTooltip: '',
  backToAuction: '',
  backToAuctionText: '',
  rightsText: 'VG Bild-Kunst, Bonn, (2016)',
  item: '',
  config: {},
  emailBody: '',
  emailSubject: '',
  emailHoverTitle: '',
  printHoverTitle: '',
  absenteeBidUrl: '',
  absenteeBidUrlForLoggedInUser: '',
  dynamicTitle: ''
};

function mapStateToProps(state) {
  return {
    lot: _.get(state, 'modules.data.lot', {}),
    auction: _.get(state, 'modules.data.auction', {}),
    userLoggedIn: !!state.user.email,
    user: state.user,
    config: state.config,
    dynamicTitle: state.modules.module.content.dynamicTitle
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(UserActions, dispatch),
    lotActions: bindActionCreators(LotActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(LotTeaserComponent);



