import React from 'react';
import {setImageSize, processHashNavigation} from 'helpers/utils';

import './recommended.scss';


export class Recommended extends React.Component {
  shouldComponentUpdate(newProps) {
    return this.props.lot.expertId !== newProps.lot.expertId ||
           this.props.lot.lotNumber !== newProps.lot.lotNumber ||
           this.props.expert.data === null ||
           this.props.expert.lang !== newProps.expert.lang ||
           this.props.extras.locale !== newProps.extras.locale;
  }

  render() {
    const expertId = this.props.lot.expertId;
    if (expertId) {
      this.getInfo(this.props.extras.locale, expertId);

      const expertInfo = this.props.expert.data;
      if (!expertInfo) return null;

      const name = expertInfo.Name;
      const img = expertInfo.MainImage;
      const image = setImageSize(this.props.imageHeight, this.props.imageWidth, img);

      return (
        <div className="recommended-by-expert" onClick={this.handleClick} title={this.props.recommendedByHoverTitle}>
          <div className="recommended-by-expert__info">
            <div>{this.props.recommendedBy}</div>
            <div>{name}</div>
          </div>
          {img && <img src={image} alt={name} />}
        </div>
      );
    }

    return null;
  };

  getInfo(locale, expertId) {
    return this.props.expertActions.getInfo(locale, expertId);
  }

  handleClick = () => {
    processHashNavigation('#recommended');
  };
}

Recommended.propTypes = {
  data: React.PropTypes.object,
  extras: React.PropTypes.object,
  lot: React.PropTypes.object,
  expertActions: React.PropTypes.object,
  expert: React.PropTypes.object,
  recommendedBy: React.PropTypes.string,
  recommendedByHoverTitle: React.PropTypes.string,
  imageWidth: React.PropTypes.number,
  imageHeight: React.PropTypes.number
};

Recommended.defaultProps = {
  imageWidth: 160,
  imageHeight: 160
};

export default Recommended;



