import _ from 'helpers/lodash';
import {renderAsString} from 'components/priceComp';
import {createSection, createBlock, createText} from 'helpers/content-type-generators';
import {businessEntityStringProcessor} from 'helpers/utils';

const FULL_WIDTH = 12;
const HALF_WIDTH = 6;
const THEMED_AUCTION = 'themed';

export function renderLotDetailsScheme(props) {
  const {
    extras,
    firstSectionTitle,
    data,
    priceDescriptionText,
    ageRestrictionText,
    soldThroughText,
    resaleRightsText
  } = props;

  const businessEntity = _.get(data, 'lot.businessEntity', 'BER');
  const ageRestriction = _.get(data, 'lot.ageRestriction', false);

  let sectionItems = [];
  let itemList = _.get(data, 'lot.features', []).slice(0);
  const isShop = _.get(data, 'lot.status.isShop', false);

  if (_.get(data, 'auction.auctionType', '') === THEMED_AUCTION && !isShop) {
    itemList.push(`${priceDescriptionText} ${createPriceItem(props)}`);
  }

  if (_.get(data, 'lot.resaleRights', false)) {
    itemList.push(resaleRightsText);
  }

  if (ageRestriction) itemList.push(ageRestrictionText);

  itemList.push(businessEntityStringProcessor(soldThroughText, businessEntity));
  itemList = itemList.filter(item => item);

  if (extras.isMobile) {
    sectionItems.push(markdownListInBlock(itemList, extras, FULL_WIDTH));
  }
  else {
    let halfLength = Math.ceil(itemList.length / 2);

    sectionItems.push(markdownListInBlock(itemList.slice(0, halfLength), extras, HALF_WIDTH));
    sectionItems.push(markdownListInBlock(itemList.slice(halfLength), extras, HALF_WIDTH));
  }

  sectionItems.push(createArtLossItem(props));
  return createSection({
    id: 'lot-details-section',
    title: firstSectionTitle,
    items: sectionItems.filter(item=>item),
    extras: extras,
    className: 'lot-accordion__details'
  });
}

export function createPriceItem(props) {
  const {data, extras} = props;
  const estimate = _.get(data, 'lot.estimate', {});
  return renderAsString(estimate.value, estimate.currency, extras.locale);
}

export function createArtLossItem(props) {
  const {artLossText, data, extras} = props;
  const artLoss = _.get(data, 'lot.artLoss', true);

  if (!artLoss) return null;

  return createBlock([
    createText(artLossText, extras, {useSimplePrerenderer: true})
  ], {
    size: FULL_WIDTH,
    className: 'lot-accordion__art-loss'
  }, extras);
}

function markdownListInBlock(itemList, extras, size) {
  const itemsString = ' - ' + itemList.join('\n - ');

  return createBlock([createText(itemsString, extras)], {
    size: size,
    className: 'lot-accordion__details-block'
  }, extras);
}



