import React, {Component} from 'react';
import _ from 'helpers/lodash';
import {connect} from 'react-redux';
import AccordionElement from 'accordion/AccordionElement';
import {renderLotDetailsScheme} from 'lot-accordion/LotDetailsElement';
import {createSection, createBlock, createText, createType} from 'helpers/content-type-generators';
import './lot-accordion.scss';

export class LotAccordionContainer extends Component {
  render() {
    const {extras, items} = this.props;

    let accordeonItems = [
      renderLotDetailsScheme(this.props),
      this.renderLotDescription(),
      this.renderReserve(),
      this.renderLotQuestion()
    ].concat(this.identifySections(items));

    accordeonItems = accordeonItems.filter(item => item);
    let expandConfig = {
      'desktop': [true],
      'mobile': [true]
    };

    return (
      <div className="lot-accordion">
        <AccordionElement sectionsExpandedObj={expandConfig} items={accordeonItems} extras={extras} />
      </div>
    );
  }

  renderLotDescription() {
    const {data, secondSectionTitle, extras} = this.props;
    let description = _.get(data, 'lot.detailedDescription', '');

    if (description) {
      description = description.replace(/\n/g, '');
    }

    if (!description) return null;

    let textBlock = createText(description, extras, {useSimplePrerenderer: true}, 'auctionware-text');

    return createSection({
      id: 'lot-description-section',
      title: secondSectionTitle,
      items: [createBlock([textBlock], {size: 12}, extras)],
      extras: extras,
      className: 'lot-accordion__description'
    });
  }

  /**
   * Reserve block section. Shown only if appropriate flag is set.
   * Text depends on business entity (Berlin or New York)
   * @returns {object}
   */
  renderReserve() {
    const {extras, data} = this.props;

    if (data.lot.hasReserve && data.auction.workflowStageName !== 'Results') {
      const businessEntity = data.lot.businessEntity;

      let textBlock = createText(this.props[`reserveDescription${businessEntity}`], extras);

      return createSection({
        id: 'reserve-section',
        title: this.props[`reserveTitle${businessEntity}`],
        items: [createBlock([textBlock], {size: 12}, extras)],
        extras: extras,
        className: 'lot-accordion__reserve-block'
      });
    }

    return null;
  }

  renderLotQuestion() {
    const {lotQuestionTitle, extras} = this.props;
    let LotQuestionBlock = createType('lot_question', this.props, extras);

    return createSection({
      id: 'lot-question-section',
      title: lotQuestionTitle,
      items: [LotQuestionBlock],
      extras: extras,
      className: 'lot-accordion__lot-question'
    });
  }

  /**
   * Get internal ID and set as className, to identify dynamic sections from contentful.
   * @param {array} items modules items
   * @returns {array}
   */
  identifySections(items) {
    return items.map((value) => {
      value.className = value.content.internalId;

      if (value.content.internalId === 'recommended') {
        const expertData = this.props.expert.data;

        if (!expertData) return null;
        value.content.title = value.content.title.replace('{name}', expertData.Name);
      }

      return value;
    });
  }
}

LotAccordionContainer.propTypes = {
  firstSectionTitle: React.PropTypes.string,
  secondSectionTitle: React.PropTypes.string,
  priceDescriptionText: React.PropTypes.string,
  ageRestrictionText: React.PropTypes.string,
  soldThroughText: React.PropTypes.string,
  resaleRightsText: React.PropTypes.string,
  artLossText: React.PropTypes.string,
  lotQuestionTitle: React.PropTypes.string,
  extras: React.PropTypes.object,
  data: React.PropTypes.object,
  items: React.PropTypes.array,
  expert: React.PropTypes.object
};

LotAccordionContainer.defaultProps = {
  data: {
    lot: {
      businessEntity: 'BER',
      hasReserve: false
    }
  },
  items: [],
  expert: {}
};

function mapStateToProps(state) {
  return {
    data: state.modules.data,
    expert: state.expert
  };
}

export default connect(mapStateToProps)(LotAccordionContainer);



