import React from 'react';
import LotQuestionForm from './LotQuestionForm';
import MarkdownElement from 'components/markdown/MarkdownElement';
import _ from 'helpers/lodash';
import {SubmissionError, reset} from 'redux-form';
import { connect } from 'react-redux';
import * as LotQuestionFormActions from 'ui/actions/LotQuestionFormActions';
import {bindActionCreators} from 'redux';
import {businessEntityStringProcessor} from '../helpers/utils.js';
import * as UserActions from 'ui/actions/UserActions';
import {postRequest} from 'helpers/rest';
import { SUBMIT_QUESTION, event } from 'helpers/gtm';

const LOT_QUESTION_FORM_POST_URL = '/_/o/{lotNumber}/questions';

export class LotQuestionComponent extends React.Component {
  componentDidMount = () => {
    if (!this.props.user.country) {
      this.props.userActions.getCountry();
    }
  }
  shouldComponentUpdate = (newProps) => {
    if (_.get(this.props, 'data.lot.lotNumber') !== _.get(newProps, 'data.lot.lotNumber')) {
      this.props.actions.isQuestionPosted(false);
    }
    return true;
  }
  submitQuestion = (values) => {
    const { extras: { log },  config: { timeout }, submitQuestionLink} = this.props;
    values.PrefferedLanguage = this.props.locale;
    const submitLink = submitQuestionLink || LOT_QUESTION_FORM_POST_URL;

    return postRequest(submitLink.replace('{lotNumber}', _.get(this.props, 'data.lot.lotNumber')), JSON.stringify(values), null, timeout).catch((error) => {
      if (error.validationErrors) {
        throw new SubmissionError(error.validationErrors);
      }
      else {
        log.error('Cannot send lot question', error);
        throw new SubmissionError({_error: [{msg: 'lotQuestionPostError'}]});
      }
    }).then(() => {
      this.props.actions.isQuestionPosted(true);
      this.props.resetForm();
      if (_.get(this.props, 'extras.isMobile')) {
        //go up on mobile
        setTimeout(()=>{
          location.hash = '#lot-question-success-message';
        }, 50);
      }
      event(SUBMIT_QUESTION);
    });
  }
  render() {
    const {extras, questionFormDescriptionText, successText, data} = this.props;
    const businessEntity = _.get(data, 'auction.businessEntity', 'BER');
    const questionFormDescription = businessEntityStringProcessor(questionFormDescriptionText, businessEntity);

    if (this.props.questionIsPosted) {
      return (
        <div className="lot-question_comp" id="lot-question-success-message">
          <MarkdownElement text={questionFormDescription} extras={extras} className="lot-question__info" />
          <div className="md-mxn2 clearfix">
            <div className="lot-question__success-wrapper mx-auto md-col-9 clearfix">
              <div className="lot-question__success md-col-3 md-col" />
              <div className="lot-question__success-message md-col-6 md-col">
                <MarkdownElement text={successText} extras={extras} />
              </div>
            </div>
          </div>
        </div>
      );
    }

    return (
      <div className="lot-question_comp">
        <MarkdownElement text={questionFormDescription} extras={extras} className="lot-question__info" />
        <LotQuestionForm onSubmit={this.submitQuestion} {...this.props} />
      </div>
    );
  }
}

LotQuestionComponent.propTypes = {
  store: React.PropTypes.object,
  actions: React.PropTypes.object,
  extras: React.PropTypes.object,
  config: React.PropTypes.object,
  data: React.PropTypes.object,
  locale: React.PropTypes.string,
  user: React.PropTypes.object,
  userActions: React.PropTypes.object,
  questionFormDescriptionText: React.PropTypes.string,
  submitQuestionLink: React.PropTypes.string,
  successText: React.PropTypes.string,
  lotQuestionPostError: React.PropTypes.string,
  questionIsPosted: React.PropTypes.bool,
  resetForm: React.PropTypes.func
};

LotQuestionComponent.defaultProps = {
  questionFormDescriptionText: 'Fill the {BER:Berlin,NYC:NewYork} form!',
  successText: '#### Your question has been received!\nAbra kadabra'
};

LotQuestionComponent.contextTypes = {};

function mapStateToProps(state) {
  return {
    data: state.modules.data,
    locale: state.modules.module.locale,
    userLoggedIn: !!state.user.email,
    user: state.user,
    config: state.config,
    questionIsPosted: state.lotQuestionForm.questionIsPosted
  };
}

function mapDispatchToProps(dispatch) {
  return {
    resetForm: () => {
      dispatch(reset('LotQuestionForm'));
    },
    actions: bindActionCreators(LotQuestionFormActions, dispatch),
    userActions: bindActionCreators(UserActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(LotQuestionComponent);



