import React from 'react';
import {STATE_IN_SHOPPING_CART} from 'ui/actions/ShopLotActions';
import ButtonElement from 'components/buttons/ButtonElement';
import gtm from 'helpers/gtm';
import _ from 'helpers/lodash';
import MarkdownElement from 'components/markdown/MarkdownElement';

import './buy-now.scss';

export class BuyNowComponent extends React.Component {
  render() {
    return this.getRenderedSection();
  };

  getRenderedSection = () => {
    const {
      buyNowDescription,
      buyNowText,
      aicButtonText,
      aicDescription,
      extras,
      showOnlyButton,
      shopLot
    } = this.props;

    if (!shopLot.state) return null;

    let buyNowProps = {
      buttonText: buyNowText,
      description: buyNowDescription,
      handler: this.addToCartAndRedirect
    };
    if (typeof window === 'undefined' || !window.isInternalUser) {
      return <div className="shop-lot-teaser__buy-now-text"><MarkdownElement text={buyNowProps.description} /></div>;
    }

    if (shopLot.state === STATE_IN_SHOPPING_CART) {
      buyNowProps.buttonText = aicButtonText;
      buyNowProps.description = aicDescription;
      buyNowProps.handler = this.redirectToCart;
    }

    return (
      <div className="buy-now">
        <ButtonElement text={buyNowProps.buttonText} buttonType="primary" extras={extras}
                       clickHandler={buyNowProps.handler} />
        {showOnlyButton ? null : <div className="shop-lot-teaser__buy-now-text">{buyNowProps.description}</div>}
      </div>
    );
  };

  addToCartAndRedirect = (e) => {
    e.preventDefault();
    const {
      shopLotActions,
      windowActions,
      shoppingCartUrl,
      config: { authUrl }
    } = this.props;

    return shopLotActions.addToCart({
      lotNumber: _.get(this.props, 'lot.lotNumber')
    }, authUrl).then(() => {
      gtm.event(gtm.BUY_NOW, {
        server: true
      });

      windowActions.changeLocation(shoppingCartUrl);
    }).catch((error) => {
      gtm.event(gtm.BUY_NOW, {
        server: false,
        error
      });
    });
  };

  redirectToCart = (e) => {
    e.preventDefault();
    this.props.windowActions.changeLocation(this.props.shoppingCartUrl);
  }
}

BuyNowComponent.propTypes = {
  data: React.PropTypes.object,
  extras: React.PropTypes.object,
  shopLotActions: React.PropTypes.object,
  windowActions: React.PropTypes.object,
  shopLot: React.PropTypes.object,
  buyNowDescription: React.PropTypes.string,
  aicButtonText: React.PropTypes.string,
  aicDescription: React.PropTypes.string,
  buyNowText: React.PropTypes.string,
  link: React.PropTypes.string,
  shoppingCartUrl: React.PropTypes.string,
  showOnlyButton: React.PropTypes.bool,
  config: React.PropTypes.object
};

export default BuyNowComponent;



