import React from 'react';
import {renderInput, renderGlobalError} from 'components/field/field';
import {Field} from 'redux-form';
import {classes} from 'helpers/utils';

const PlaceOfferForm = (props) => {
  const formClassName = classes({
    'form-submitting': props.submitting
  });

  return (
    <form onSubmit={props.handleSubmit} className={formClassName}>
      <label>
        <Field name="placeBid" component={renderInput} props={props} />
      </label>
      <p>{props.counterOfferText}</p>
      <button className="button-element primary-button button-submit" type="submit"
              disabled={!props.valid || props.submitting}>{props.submitYourOffer}</button>
      <div className="global-error">{renderGlobalError(props)}</div>
    </form>
  );
};

PlaceOfferForm.propTypes = {
  submitting: React.PropTypes.bool,
  error: React.PropTypes.array,
  handleSubmit: React.PropTypes.func,
  valid: React.PropTypes.bool,
  counterOfferText: React.PropTypes.string,
  submitYourOffer: React.PropTypes.string
};

export default PlaceOfferForm;



