/**
  ### validate usage:

  ## Create validator:

  const myCustomValidator = createValidator({
    username: [required(msgpath), LongerThan(15)(msgpath), combine(fn1,fn2)],
    password: [required(msgpath), LongerThan(8)(msgpath), combine(fn1,fn2)],
    passwordagain: [required(msgpath), LongerThan(8)(msgpath), equalTo(passwordKey)(msgpath)],
  });

  ## write validate rule

  export let equalTo = (passwordKey) => (message) => (value, values, key) => {
    return values[passwordKey] &&  values[passwordKey] === value && message;
  }

  ## use middleware on server
  app.post("/path/to/post", validationMiddleware(myCustomValidator), doPostIfValidationOk);

*/
const bidIncrement = require('./helpers/bidIncrement');

const validateEmail = function validateEmail(email) {
  var re = /^\b[A-Z0-9._%\-+]+@[A-Z0-9.\-]+\.[A-Z]{2,7}\b$/i;
  return re.test(email);
};

module.exports = {};
module.exports.required = function (message) {
  return function (value) {
    //value, values, key
    if (value) {
      return !String(value).trim() && message;
    }
    return message;
  };
};

module.exports.longerThan = function (len) {
  return function (message) {
    return function (value) {
      return value.length < len && message;
    };
  };
};

module.exports.lessThan = function (maxValue) {
  return function (message) {
    return function (value) {
      return value > maxValue && message;
    };
  };
};

module.exports.lessThanWithProps = function(message, get) {
  return function(value, values, key, props) {
    return value > get(props) && message;
  };
};

module.exports.greaterThan = function(minValue) {
  return function (message) {
    return function (value) {
      return value < minValue && message;
    };
  };
};

module.exports.greaterThanEqual = function(minValue) {
  return function (message) {
    return function (value) {
      return value <= minValue && message;
    };
  };
};

module.exports.greaterThanWithProps = function(message, get) {
  return function(value, values, key, props) {
    return value < get(props) && message;
  };
};

module.exports.isValidEmail = function (message) {
  return function (value) {
    return !validateEmail(value) && message;
  };
};

module.exports.isBidValidStep = function (message) {
  return function (bid) {
    return !bidIncrement.isValid(bid) && message;
  };
};

/**
 * Allowed only numbers, space, -, +, (, )
 * @param {string} message error message
 * @returns {Function}
 */
module.exports.isValidPhoneNumber = function (message) {
  return function (value) {
    return !(/^[\d\(\)\-\+\s]+$/.test(value)) && message;
  };
};

module.exports.isValidInteger = function (message) {
  return function (value) {
    var re = /^\d+$/;
    return !re.test(value) && message;
  };
};

module.exports.validationMiddleware = function () {
  var createError = arguments.length <= 0 || arguments[0] === undefined ? function (err) {
    return { validationErrors: err };
  } : arguments[0];
  return function (validator) {
    return function (req, res, next) {
      var errors = validator(req.body);
      if (typeof(req.body) === 'object' && req.body !== null) {
        if (Object.keys(errors).length > 0) {
          return next(createError(errors));
        }
      }
      return next();
    };
  };
};

module.exports.createValidator = function createValidator(validateObject) {
  return function (values, props) {
    var errors = {};
    values = values || {};
    Object.keys(validateObject).forEach(function (field) {
      errors[field] = validateObject[field].reduce(function (error, handler) {
        if (error) return error;
        return handler(values[field], values, field, props);
      }, false);
      if (!errors[field]) {
        delete errors[field];
      }
      else {
        errors[field] = Array.isArray(errors[field]) ? errors[field] : [errors[field]];
      }
    });
    return errors;
  };
};

module.exports.combine = function combine() {
  var handlers = [].slice.call(arguments);
  return function (value, values, key) {
    var errors = handlers.reduce(function (allErrors, handler) {
      var result = handler(value, values, key);
      if (result) {
        allErrors = allErrors.concat(result);
      }
      return allErrors;
    }, []);
    if (errors.length > 0) return errors;
    return false;
  };
};



