import React, { Component, PropTypes } from 'react';
import MarkdownElement from 'components/markdown/MarkdownElement';
import {AuctionLotCalculatorForm, ShopLotCalculatorForm} from './LotCalculatorForm';
import LotCostsComponent from './LotCostsComponent';
import {SubmissionError} from 'redux-form';
import {connect} from 'react-redux';
import _ from 'helpers/lodash';
import './lot-calculator.scss';
import gtm from 'helpers/gtm';
import {postRequest} from 'helpers/rest';
import {camelize, businessEntityStringProcessor} from 'helpers/utils';

export class LotCalculatorComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      costs: {},
      region: '',
      localPickup: false
    };
  }

  componentWillReceiveProps(newProps) {
    const { lot: { lotNumber } } = newProps;
    if (lotNumber !== this.props.lot.lotNumber) {
      this.setState({ costs:{} });
    }
  }

  render() {
    const {
      generalNotesText,
      lot,
      priceNotesText,
      priceNotesTextWithTaxes,
      shippingInsuranceText
    } = this.props;

    const { costs, region, localPickup } = this.state;
    const isShop = _.get(lot, 'status.isShop', false);
    const initialValues = {
      amount: isShop ? lot.totalPrice.value : lot.startingBid.value
    };

    let priceNotes = priceIncludeTaxes(lot.businessEntity, region)
      ? priceNotesTextWithTaxes
      : priceNotesText;

    if (!costs.total) {
      priceNotes = '';
    }
    
    const CalculatorForm = isShop ? ShopLotCalculatorForm : AuctionLotCalculatorForm;

    return (
      <div className="lot-calculator" key={lot.lotNumber}>
        <CalculatorForm
          initialValues={initialValues}
          startingPriceItem={isShop ? lot.totalPrice : lot.startingBid}
          onSubmit={this.handleSubmit}
          handleBlur= {this.handleBlur}
          {...this.props} />
        <div className="lot-calculator__details sm-col sm-col-12">
          <div className="sm-col md-col-4 sm-col-12">
            <MarkdownElement className="lot-calculator__general-notes"
                             text={businessEntityStringProcessor(generalNotesText, lot.businessEntity)} />
          </div>
          <div className="sm-col md-col-8 sm-col-12 lot-calculator__costs-wrapper">
            <div className="sm-col sm-col-12 lot-calculator__costs">
              <LotCostsComponent
                localPickup={localPickup}
                costs={costs}
                currency={lot.startingBid.currency}
                {...this.props}
                priceNotesText={priceNotes}
                shippingInsuranceText={businessEntityStringProcessor(shippingInsuranceText, lot.businessEntity)} />
            </div>
          </div>
        </div>
      </div>
    );
  }

  handleSubmit = (values) => {
    const {lot, bookingLinesEndpoint, config: { timeout }, locations, extras: { log }} = this.props;
    const isShop = _.get(lot, 'status.isShop', false);
    const selectedLocation  = _.first(locations
      .filter(l => l.content.locationKey === values.locationKey)
      .map(l => l.content));
    const startingPriceKey = isShop ? 'counterOffer' : 'bid';

    values.localPickup = !!selectedLocation.localPickup;
    values.region = selectedLocation.locationCode.trim();

    let bodyMessage = Object.assign({}, values);
    bodyMessage[startingPriceKey] = values.amount;
    delete bodyMessage.amount;

    this.setState({costs: {}});
    return postRequest(bookingLinesEndpoint.replace('{lotNumber}', lot.lotNumber), JSON.stringify(bodyMessage), null, timeout)
      .then((res) => {
        this.setState({
          costs: camelize(res),
          region: values.region,
          localPickup: values.localPickup
        });
        gtm.event(gtm.ABSENTEE_BIDDING_CALCULATION, {
          bidValue: _.get(values, 'bid'),
          calculationResult: _.get(res, 'Total.Value')
        });
      })
      .catch((error) => {
        log.error('Cannot calculate costs', error);
        throw new SubmissionError({_error: [{msg:'priceCalculatorPostError'}]});
        this.setState({costs: {}, region: ''});
      });
  }

  handleBlur = () => {
    this.setState({costs: {}, region: ''});
  }
}

LotCalculatorComponent.propTypes = {
  extras: PropTypes.object,
  auction: PropTypes.object,
  config: PropTypes.object,
  lot: PropTypes.object,
  locations: PropTypes.array,
  locationLabel: PropTypes.string,
  generalNotesText: PropTypes.string,
  priceNotesText: PropTypes.string,
  priceNotesTextWithTaxes: PropTypes.string,
  shippingInsuranceText: PropTypes.string,
  bookingLinesEndpoint: PropTypes.string,
  priceCalculatorPostError: PropTypes.string
};

LotCalculatorComponent.defaultProps = {
  auction: {},
  lot: {},
  locations: [],
  extras: {}
};

function mapStateToProps(state) {
  return {
    auction: state.modules.data.auction,
    lot: state.modules.data.lot,
    config: state.config,
    bookingLinesEndpoint: state.config.bookingLinesEndpoint
  };
}

function priceIncludeTaxes(businessEntity, region) {
  return businessEntity == 'BER' && (region == 'EUC' || region == 'GER');
}

export default connect(mapStateToProps)(LotCalculatorComponent);



