import React from 'react';
import bidIncrement from 'helpers/bidIncrement';
import _ from 'helpers/lodash';
import {classes} from 'helpers/utils';

import './style.scss';

const EMAIL_REGEX = /\<([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*|".+")@(\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}]|[a-zA-Z\-0-9]+\.+[a-zA-Z]{2,})\>/g;

const errorsComp = (props) => {
  const { meta: { touched, error } } = props;
  if (!touched || !error) return null;
  return error.map((err, index) => {
    const message = err.errorMessage || _.get(props, err.msg, `[code:${err.msg}]`)
                                            .replace(EMAIL_REGEX, '<a href="mailto:$1@$3">$1@$3</a>');
    return err && (<div className="message-error" key={index} dangerouslySetInnerHTML={{__html: message}} />);
  });
};

export const renderGlobalError = props => {
  if (!props || !props.error) return null;
  return errorsComp({meta: {touched: true, error: props.error}, ...props});
};

export const renderInput = (props) => {
  const { input, type, meta: { touched, error }, disabled } = props;
  const fieldClass = classes({
    'field-error': error && error.length && touched,
    'field-wrapper': true
  });

  return (
    <div className={fieldClass}>
      <div className="input-wrapper field-element-wrapper">
        <input {...input} type={type} disabled={disabled} />
      </div>
      {errorsComp(props)}
    </div>
  );
};

renderInput.propTypes = {
  input: React.PropTypes.object,
  label: React.PropTypes.string,
  type: React.PropTypes.string,
  disabled: React.PropTypes.bool,
  meta: React.PropTypes.object
};

export const renderBidInput = (props) => {
  let {
    input: { value, onChange },
    min
  } = props;
  let nextValue = bidIncrement.up(value);
  let prevValue = bidIncrement.down(value);
  const fieldClass = classes({
    'field-error': props.meta.error && props.meta.error.length && props.meta.touched,
    'field-wrapper': true
  });
  if (min && prevValue < min) {
    prevValue = min;
  }
  if (min && nextValue < min) {
    nextValue = min;
  }
  if (min && !value) {
    nextValue = min;
    prevValue = min;
  }
  return (
    <div className={fieldClass}>
      <div className="input-wrapper field-element-wrapper">
        <input {...props.input} placeholder={props.placeholder} type="text" />
        <a onClick={()=>onChange(nextValue)} className="input-bid__up" />
        <a onClick={()=>onChange(prevValue)} className="input-bid__down" />
      </div>
      {errorsComp(props)}
    </div>
  );
};

renderBidInput.propTypes = {
  input: React.PropTypes.object,
  meta: React.PropTypes.object,
  min: React.PropTypes.number,
  placeholder: React.PropTypes.string
};

  /**
   * Render dropdown for redux-form
   * @param  {Object} props
   * @return {Dropdown}
   */
export const renderSelect = (props) => {

  const fieldClass = classes({
    'field-error': props.meta.error && props.meta.error.length && props.meta.touched,
    'field-wrapper': true
  });
  return (
    <div className={fieldClass}>
      <div className="select-wrapper field-element-wrapper">
        <select {...props.input} placeholder={ props.placeholder }>
          {props.selectOptions.map((opt, index) =><option key={index}
                                              value={opt.key}
                                              disabled={opt.key==''}
                                              hidden={opt.key==''}>
            {opt.value}
          </option>)}
        </select>
      </div>
      {errorsComp(props)}
    </div>
  );
};

renderSelect.propTypes = {
  input: React.PropTypes.object,
  meta: React.PropTypes.object,
  selectOptions: React.PropTypes.array,
  placeholder: React.PropTypes.string
};

export  const renderTextarea = (props) => {
  const fieldClass = classes({
    'field-error': props.meta.error && props.meta.error.length && props.meta.touched,
    'field-wrapper': true
  });
  return (
    <div className={fieldClass}>
      <div className="textarea-wrapper field-element-wrapper"><textarea {...props.input} /></div>
      {errorsComp(props)}
    </div>
  );
};

renderTextarea.propTypes = {
  input: React.PropTypes.object,
  meta: React.PropTypes.object,
  placeholder: React.PropTypes.string
};

//Could be rewritten in renderSelect style
export  const renderCheckbox = (label) => {
  let render = (props) => {
    //props.checked = props.value;
    let newProps = {
      ...props.input,
      checked: props.input.value ? true : undefined,
      id: `${props.input.name}_id`,
      className: props.input.value ? 'active' : 'inactive'
    };
    const fieldClass = classes({
      'field-error': props.meta.error && props.meta.error.length && props.meta.touched,
      'field-wrapper': true
    });
    const checkClass = classes({'checked': props.input.value, 'checkbox-check': true});
    return (
      <div className={fieldClass}>
        <div className="checkbox-wrapper">
          <div className="checkbox">
            <div className={checkClass} /><input type="checkbox" {...newProps} />
          </div>
          <label htmlFor={newProps.id}>{label}</label>
        </div>
        {errorsComp(props)}
      </div>
    );
  };

  render.propTypes = {
    input: React.PropTypes.object,
    meta: React.PropTypes.object,
    placeholder: React.PropTypes.string,
    checked: React.PropTypes.bool,
    name: React.PropTypes.string
  };

  return render;
};



