import React from 'react';
import * as UserActions from 'ui/actions/UserActions';
import * as LotActions from 'ui/actions/LotActions';
import * as ShopLotActions from 'ui/actions/ShopLotActions';
import * as ExpertActions from 'ui/actions/ExpertActions';
import * as WindowActions from 'ui/actions/WindowActions';
import {
  STATE_RESERVED,
  STATE_IN_SHOPPING_CART,
  STATE_PLACE_OFFER,
  STATE_INCREASE_OFFER,
  STATE_SOLD
} from 'ui/actions/ShopLotActions';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import _ from 'helpers/lodash';
import GalleryComponent from 'gallery/GalleryComponent';
import LinkComp from 'components/linkComp';
import MarkdownElement from 'components/markdown/MarkdownElement';
import PriceComp from 'components/priceComp';
import PriceTooltip from 'components/price-tooltip/PriceTooltip';
import VGBildPlaceholder from 'components/vgbild-placeholder/VGBildPlaceholder';

import BuyNow from './buy-now/BuyNowComponent';
import ActionButtons from './action-buttons/ActionButtons';
import Recommended from './recommended/Recommended';
import Sold from './sold/Sold';
import Reserved from './reserved/Reserved';
import WatchingCounter from './watching-counter/WatchingCounter';
import {classes} from '../helpers/utils.js';

import './shop-lot-teaser.scss';

export class ShopLotTeaserComponent extends React.Component {
  componentWillReceiveProps(nextProps) {
    const lotNumber = _.get(this.props, 'lot.lotNumber');
    const nextLotNumber = _.get(nextProps, 'lot.lotNumber', -1);

    if (lotNumber !== nextLotNumber && nextLotNumber > 0) {
      this.props.lotActions.visitLotPage({
        lotNumber: nextLotNumber,
        token: _.get(this.props, 'config.authToken')
      });
    }
  }

  shouldComponentUpdate(nextProps) {
    const nextLotNumber = _.get(nextProps, 'lot.lotNumber', -1);
    return nextLotNumber > 0;
  }

  render() {
    const {
      lot,
      rightsText,
      itemNumber,
      salePriceText,
      startingPriceText,
      soldPriceText
    } = this.props;
    const title = _.get(lot, 'title', 'NO TITLE');
    const isWhitelistedLot = _.get(lot, 'requiresWhitelistMapping', false);
    const price = this.isSold() ? lot.soldPrice : lot.totalPrice;

    const rightsIndicator = lot.representedVgBild ? <div className="shop-lot-teaser__rights-text">{rightsText}</div> :
      null;

    const section = this.getRenderSection();
    const saleSection = this.getSaleSection();
    const shopLot = this.props.shopLot || {};
    const reservedByOther = !_.get(shopLot, 'personalStatus.CheckoutUrl') && shopLot.state === STATE_RESERVED;
    const priceLabel = this.isSale(lot) && !this.isSold() ? salePriceText : startingPriceText;
    const priceClasses = classes({
      'shop-lot-teaser__price': true,
      'fade': reservedByOther || this.isSold()
    });
    const itemNumberText = lot.valuationCaseNumber && lot.valuationCaseIndex ?
      `${itemNumber} ${lot.valuationCaseNumber}-${lot.valuationCaseIndex}` : '';

    const protectedSoldLot = lot.representedVgBild && this.isSold();
    const gallery = protectedSoldLot ? <VGBildPlaceholder /> : <GalleryComponent {...this.props} />;

    return (
      <div className="section-wrapper clearfix shop-lot-teaser px1">
        <div className="col-12 clearfix">
          <div className="clearfix">
            <div className="left">
              {this.renderBackLink()}
            </div>
            <div className="right">
              {/*<LotsNavigation {...this.props} />*/}
            </div>
          </div>
          {rightsIndicator}
        </div>

        {isWhitelistedLot && this.renderWhitelistWarning()}
        <div className="sm-col-12 md-col-8 md-col">
          {gallery}
        </div>
        <div className="sm-col-12 md-col-4 md-col shop-lot-teaser__description_wrapper">
          <h1 className="shop-lot-teaser__title">{title}</h1>
          <span className="shop-lot-teaser__lot-number">{itemNumberText}</span>
          <div className="shop-lot-teaser__sale-info">
            {saleSection}
          </div>
          <div className={priceClasses}>
            <div>
              {this.isSold() ? soldPriceText : priceLabel}
              <PriceComp value={price.value} currency={price.currency} extras={this.props.extras} />
            </div>
            <div className="shop-lot-teaser__tooltips-container">
              <PriceTooltip {...this.props} />
              <WatchingCounter {...this.props} />
            </div>
          </div>

          <div className="shop-lot-teaser__info-section">
            <div className="shop-lot-teaser__info-sections">
              {section}
            </div>

            {this.props.children}

            <Recommended {...this.props} />
            <ActionButtons {...this.props} />
          </div>
        </div>
      </div>
    );
  }

  componentDidMount() {
    const {userLoggedIn, auction, lot, config} = this.props;
    if (userLoggedIn && auction.auctionNumber) {
      this.props.actions.getAuctionData(auction.auctionNumber);
    }

    if (lot.lotNumber > 0) {
      this.props.shopLotActions.getPersonalStatus({
        lotNumber: lot.lotNumber
      }, config.authUrl);

      this.props.lotActions.visitLotPage({
        lotNumber: lot.lotNumber,
        token: config.authToken
      });
    }
  }

  getSaleSection() {
    const {lot} = this.props;

    if (!this.isSale(lot) || this.isSold()) return null;

    return (
      <div className="shop-lot-teaser__sale-wrapper">
        <div className="shop-lot-teaser__sale-description">
          {this.props.saleText}
        </div>
        <div className="shop-lot-teaser__sale-old-price-wrapper">
          <div className="shop-lot-teaser__sale-old-price-line" />
          <div className="shop-lot-teaser__sale-old-price-container">
            {this.props.startingPriceText}
            <PriceComp value={lot.originalPrice.value} currency={lot.originalPrice.currency}
                       extras={this.props.extras} />
          </div>
        </div>
      </div>
    );
  }

  isSale(lot) {
    return !!lot.reducePercentage;
  }

  isSold() {
    return _.get(this.props, 'lot.soldPrice.value');
  }

  getRenderSection() {
    const states = {
      [STATE_RESERVED]: () => <Reserved {...this.props} />,
      [STATE_IN_SHOPPING_CART]: () => <BuyNow {...this.props} />,
      [STATE_PLACE_OFFER]: () => <BuyNow {...this.props} />,
      [STATE_INCREASE_OFFER]: () => <BuyNow {...this.props} showOnlyButton />,
      [STATE_SOLD]: () => <Sold {...this.props} />
    };

    const component = states[this.props.shopLot.state];
    if (component) return component();
  }

  /**
   * Show back link.
   */
  renderBackLink() {
    const {extras} = this.props;

    let text = this.props.backToAuctionText;
    let linkProps = {
      link: 'javascript:window.history.back();',
      extras: extras,
      spaLink: null,
      className: 'link link--with-icon link--back'
    };

    return (<div className="shop-lot-teaser__back-to-auction">
        <LinkComp {...linkProps}>
          {text}
        </LinkComp>
      </div>
    );
  }

  renderWhitelistWarning() {
    const {whiteListWarningText} = this.props;
    return <div className="shop-lot-teaser__white-list-warning">
      <MarkdownElement text={whiteListWarningText} />
    </div>;
  }
}

ShopLotTeaserComponent.propTypes = {
  lot: React.PropTypes.object,
  auction: React.PropTypes.object,
  actions: React.PropTypes.object,
  extras: React.PropTypes.object,
  shopLot: React.PropTypes.object,
  shopLotActions: React.PropTypes.object,
  children: React.PropTypes.array,
  user: React.PropTypes.object,
  userLoggedIn: React.PropTypes.bool,
  backToAuction: React.PropTypes.string,
  backToAuctionText: React.PropTypes.string,
  lotActions: React.PropTypes.object,
  categories: React.PropTypes.object,
  rightsText: React.PropTypes.string,
  soldPriceText: React.PropTypes.string,
  config: React.PropTypes.object,
  whiteListWarningText: React.PropTypes.string,
  auctionTakesPlaceText: React.PropTypes.string,
  auctionLinkText: React.PropTypes.string,
  startingPriceText: React.PropTypes.string,
  salePriceText: React.PropTypes.string,
  saleText: React.PropTypes.string,
  itemNumber: React.PropTypes.string
};

ShopLotTeaserComponent.defaultProps = {
  config: {},
  rightsText: 'VG Bild-Kunst, Bonn, (2016)'
};

function mapStateToProps(state) {
  return {
    lot: _.get(state, 'modules.data.lot', {}),
    auction: _.get(state, 'modules.data.auction', {}),
    userLoggedIn: !!state.user.email,
    user: state.user,
    config: state.config,
    dynamicTitle: state.modules.module.content.dynamicTitle,
    shopLot: state.shopLot,
    expert: state.expert
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(UserActions, dispatch),
    lotActions: bindActionCreators(LotActions, dispatch),
    shopLotActions: bindActionCreators(ShopLotActions, dispatch),
    expertActions: bindActionCreators(ExpertActions, dispatch),
    windowActions: bindActionCreators(WindowActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ShopLotTeaserComponent);



