import React from 'react';
import _ from 'helpers/lodash';
import MobileGalleryComponent from './mobile/MobileGalleryComponent';
import DesktopGalleryComponent from './desktop/DesktopGalleryComponent';

export const GalleryComponent = (props) => {
  const lot = props.lot;

  const labels = {
    lotGalleryViewLess: props.lotGalleryViewLess,
    lotGalleryViewMore: props.lotGalleryViewMore,
    lotGalleryWatchVideo: props.lotGalleryWatchVideo,
    lotGalleryEnlargeImage: props.lotGalleryEnlargeImage,
    unavailableVideoMessage: _.get(props, 'unavailableVideoMessage.content.text', '')
  };

  const params = {
    images: lot.images.slice(0, -1),
    video: lot.lotVideo,
    caption: lot.title,
    labels: labels,
    extras: props.extras,
    config: props.config,
    features: lot.features.filter(item => item)
  };

  const Component = props.extras.isMobile ? MobileGalleryComponent : DesktopGalleryComponent;

  return <Component {...params} />;
};

GalleryComponent.propTypes = {
  lot: React.PropTypes.object,
  extras: React.PropTypes.object,
  config: React.PropTypes.object,
  lotVideo: React.PropTypes.string,
  lotGalleryViewLess: React.PropTypes.string,
  lotGalleryViewMore: React.PropTypes.string,
  lotGalleryWatchVideo: React.PropTypes.string,
  lotGalleryEnlargeImage: React.PropTypes.string,
  unavailableVideoMessage: React.PropTypes.object
};

export default GalleryComponent;



