import React from 'react';
import {connect} from 'react-redux';
import ButtonElement from 'components/buttons/ButtonElement';
import MarkdownElement from 'components/markdown/MarkdownElement';
import LinkComp from 'components/linkComp';
import _ from 'helpers/lodash';
import moment from 'moment';

import './reserved.scss';

export class Reserved extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      timer: '--:--'
    };
  }

  render() {
    const section = this.isCurrentUser() ? this.getCurrentUserSection() : this.getOtherUserSection();

    return (
      <div className="reserved">
        {section}
      </div>
    );
  }

  isCurrentUser() {
    return !!this.getCheckoutURL();
  }

  getCheckoutURL() {
    return _.get(this.props.shopLot, 'personalStatus.CheckoutUrl');
  }

  getCurrentUserSection() {
    const props = this.props;

    return (
      <div className="current-user">
        <div className="item-reserved">{props.thisItemIsReserved}</div>
        {this.getCountDownText(props.itemIsReservedForYou)}
        <ButtonElement text={props.continueWithCheckout} buttonType="primary" extras={props.extras}
                       url={this.getCheckoutURL()} />
        <MarkdownElement text={props.itemIsStillInYourCart} className="sg-small-text" />
      </div>
    );
  }

  getOtherUserSection() {
    const { lot,
      thisItemIsReserved,
      itemIsReservedForAnother,
      findOtherItems,
      extras
    } = this.props;

    return (
      <div className="other-user">
        <div className="item-reserved">{thisItemIsReserved}</div>
        {this.getCountDownText(itemIsReservedForAnother)}
        <LinkComp link={_.get(lot, 'status.location')} className="link link--with-icon"
                  extras={extras}>{findOtherItems}</LinkComp>
      </div>
    );
  }

  getCountDownText(text) {
    const remainTime = _.get(this.props.shopLot, 'personalStatus.CheckoutRemainingTime');

    this.startUpdatingCountDown(remainTime);

    const countDownText = text.split(/{(--:-- \S+)}/);

    if (countDownText.length === 1) { // can't find by regexp
      return (
        <div>{text}</div>
      );
    }

    const minutesText = countDownText[1].split(' ').pop();

    return (
      <div>
        {countDownText[0]}
        <span className="timer">{this.state.timer} {minutesText}</span>
        {countDownText[countDownText.length - 1]}
      </div>
    );
  }

  startUpdatingCountDown(remainTime = 0) {
    if (this.t) return;

    this.t = setInterval(() => {
      const remains = remainTime > 0 ? remainTime-- : remainTime;

      this.setState({
        timer: moment(moment.duration(remains, 'seconds')._data).format('mm:ss')
      });

      if (remains <= 0) return this.finish();
    }, 1000);
  }

  finish() {
    clearInterval(this.t);
  }

  /**
   * @override
   */
  componentWillUnmount() {
    this.finish();
  }
}


Reserved.propTypes = {
  extras: React.PropTypes.object,
  shopLot: React.PropTypes.object,
  lot: React.PropTypes.object,
  itemIsReservedForYou: React.PropTypes.string,
  itemIsReservedForAnother: React.PropTypes.string,
  thisItemIsReserved: React.PropTypes.string,
  findOtherItems: React.PropTypes.string,
  continueWithCheckout: React.PropTypes.string,
  itemIsStillInYourCart: React.PropTypes.string,
  countDownTimer: React.PropTypes.number
};

function mapStateToProps(state) {
  return {
    shopLot: state.shopLot
  };
}

export default connect(mapStateToProps, null)(Reserved);



