import React from 'react';
import {STATE_PLACE_OFFER_EXPANDED, STATE_INCREASE_OFFER} from 'ui/actions/ShopLotActions';
import LinkComp from 'components/linkComp';
import {reduxForm, SubmissionError} from 'redux-form';
import validate from 'validate';
import ButtonElement from 'components/buttons/ButtonElement';
import {getCurrency} from 'helpers/utils';
import gtm from 'helpers/gtm';
import PlaceOfferForm from './PlaceOfferForm';

import './counter-offer.scss';


export default class PlaceOffer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isExpanded: props.isExpanded
    };
  }

  render() {
    const section = this.state.isExpanded ? this.renderExpandSection() : this.renderCollapseSection();

    return (
      <div className="place-offer">
        {section}
      </div>
    );
  }

  renderCollapseSection() {
    return (
      <div className="counter-offer__section counter-offer__section--collapsed">
        <ButtonElement id="counter-offer__place-button" buttonType="secondary" text={this.props.placeCounterOfferText} clickHandler={this.toggleShow}
                       className="shop-lot-teaser__counteroffer"
        />
      </div>
    );
  }

  renderExpandSection() {
    const lot = this.props.lot;
    const currency = getCurrency(this.props.lot.businessEntity);
    const title = `${this.props.yourOfferIn} ${currency}`;

    const validator = validate.createValidator({
      placeBid: [
        validate.required({msg: 'requiredFieldError'}),
        validate.isValidInteger({msg: 'errorOnlyNumbers'}),
        validate.greaterThan(1)({msg: 'minError'}),
        validate.lessThan(lot.totalPrice.value - 1)({msg: 'maxError'})
      ]
    });

    const FormComponent = reduxForm({
      form: 'PlaceOfferForm',
      validate: validator,
      keepDirtyOnReinitialize: true,
      enableReinitialize: true,
      destroyOnUnmount: false
    })(PlaceOfferForm);

    return (
      <div className="counter-offer__section counter-offer__section--expanded">
        <div className="counter-offer__section-title">
          <h3>{title}</h3>
          <LinkComp extras={this.props.extras} onClick={this.toggleShow} className="link link--with-icon link--close"
                    title={this.props.closeCounterOfferTooltip} />
        </div>
        <FormComponent onSubmit={this.saveData} {...this.props} />
      </div>
    );
  }

  saveData = (data) => {
    const { extras: { log, locale }, authUrl} = this.props;

    return this.props.shopLotActions.placeCounterOffer({
      lotNumber: this.props.lot.lotNumber,
      lang: locale,
      ...data
    }, authUrl).then((result) => {
      if (result.error) {
        gtm.event(gtm.PLACE_COUNTER_OFFER, {
          event: 'coValueError',
          coValue: data.placeBid,
          errormessage: result.error
        });

        log.error('Cannot place counter offer', result.error);
        throw new SubmissionError({_error: [{msg: 'cannotPlaceCounterOffer'}]});
      }

      gtm.event(gtm.PLACE_COUNTER_OFFER, {
        event: 'coValueSuccess',
        coValue: data.placeBid
      });

      this.props.windowActions.changeLocation(result.RedirectUrl);
    });
  };

  toggleShow = (e) => {
    e.preventDefault();

    this.setState({
      isExpanded: !this.state.isExpanded
    });

    if (this.props.shopLot.state === STATE_PLACE_OFFER_EXPANDED) {
      this.props.shopLotActions.changeState(STATE_INCREASE_OFFER);
    }
  };

}

PlaceOffer.propTypes = {
  lot: React.PropTypes.object,
  extras: React.PropTypes.object,
  shopLotActions: React.PropTypes.object,
  windowActions: React.PropTypes.object,
  shopLot: React.PropTypes.object,
  isExpanded: React.PropTypes.bool,
  placeCounterOfferText: React.PropTypes.string,
  yourOfferIn: React.PropTypes.string,
  counterOfferText: React.PropTypes.string,
  submitYourOffer: React.PropTypes.string,
  cannotPlaceCounterOffer: React.PropTypes.string,
  closeCounterOfferTooltip: React.PropTypes.string,
  authUrl: React.PropTypes.string
};



