import React from 'react';
import PrintButton from 'components/action-button-print/PrintButton';
import EmailButton from 'components/action-button-email/EmailButton';
import LotWishlistContainer from 'lot-wishlist/LotWishlistContainer';
import LinkElement from 'components/link-element/LinkElement';
import {processHashNavigation} from 'helpers/utils';

export default class ActionButtons extends React.Component {
  questionButtonClick = (e) => {
    e.preventDefault();

    processHashNavigation(this.props.lotQuestionLink);
  };

  render() {
    const props = this.props;

    return (
      <div className="shop-lot-teaser__action-button">
        <EmailButton
          body={props.emailBody}
          subject={props.emailSubject}
          title={props.emailHoverTitle}
          dynamicTitle={props.dynamicTitle}
          userEmail={props.user.email}
        />
        {!props.extras.isHandheld && <PrintButton title={props.printHoverTitle} />}
        <LinkElement id="question_button" link={props.lotQuestionLink} className="link link--action-button link--question"
                     title={props.lotQuestionHoverText} onClick={this.questionButtonClick}
        />
        <LotWishlistContainer lotNumber={props.lot.lotNumber}
                              savedLabel={props.lotWishlistSaved}
                              unsavedLabel={props.lotWishlistUnsaved}
                              tooltip={props.lotWishlistTooltip}
        />
      </div>
    );
  }
};

ActionButtons.propTypes = {
  lot: React.PropTypes.object,
  extras: React.PropTypes.object,
  user: React.PropTypes.object,
  userLoggedIn: React.PropTypes.bool,
  lotWishlistSaved: React.PropTypes.string,
  lotWishlistUnsaved: React.PropTypes.string,
  lotWishlistTooltip: React.PropTypes.string,
  lotQuestionLink: React.PropTypes.string,
  lotQuestionHoverText: React.PropTypes.string,
  emailBody: React.PropTypes.string,
  emailSubject: React.PropTypes.string,
  emailHoverTitle: React.PropTypes.string,
  dynamicTitle: React.PropTypes.string,
  printHoverTitle: React.PropTypes.string
};



