import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import LinkElement from 'components/link-element/LinkElement';
import {buildAuctionLink} from 'helpers/utils';
import constants from 'const';
import _ from 'helpers/lodash';

import './styles.scss';

export const ShopEventLotBanner = (props) => {
  const { auction: { auctionType } = {}} = props;
  if (auctionType !== constants.AUCTION_SHOP_EVENT) return null;

  const isMobile = props.extras.isMobile;

  const styles = {
    backgroundImage: isMobile ? null : `url('${_.get(props.backgroundImage, 'content.img.content.file.url')}')`
  };

  const handleClick = () => {
    window.location = buildAuctionLink(props.auction, props.extras);
  };

  return (
    <div className="shop-event-lot-banner section-wrapper px1">
        <div className="shop-event-lot-banner-wrapper" style={styles} onClick={handleClick}>
          <p>{props.topText}</p>
          <h3>{props.auction.title}</h3>
          <LinkElement spaLink={buildAuctionLink(props.auction, props.extras)} useDefaultIcon label={props.linkText} />
        </div>
    </div>
  );
};

function mapStateToProps(state) {
  return {
    auction: state.modules.data.auction
  };
}

ShopEventLotBanner.propTypes = {
  extras: PropTypes.object,
  auction: PropTypes.object,
  topText: PropTypes.string,
  linkText: PropTypes.string,
  backgroundColor: PropTypes.object,
  backgroundImage: PropTypes.object
};

ShopEventLotBanner.defaultProps = {
  extras: {}
};

export default connect(mapStateToProps)(ShopEventLotBanner);



