import React from 'react';
import {STATE_PLACE_OFFER_EXPANDED} from 'ui/actions/ShopLotActions';
import PriceComp from 'components/priceComp';
import ButtonElement from 'components/buttons/ButtonElement';

import './counter-offer.scss';


export default class IncreaseOffer extends React.Component {
  render() {
    const lot = this.props.lot;
    const counterOffer = this.props.shopLot.personalStatus.PendingCounterOffers[0];
    const price = <PriceComp value={counterOffer} currency={lot.totalPrice.currency} extras={this.props.extras} />;
    const showButton = !this.props.hideButton;

    return (
      <div className="increase-offer">
        <h3>{this.props.yourOffer} {price}</h3>
        <p>{this.props.counterOfferUnderNegotiation}</p>
        {showButton && <ButtonElement buttonType="secondary" text={this.props.increaseYourOffer} clickHandler={this.showPlaceOffer} />}
      </div>
    );
  }

  showPlaceOffer = (e) => {
    e.preventDefault();

    this.props.shopLotActions.changeState(STATE_PLACE_OFFER_EXPANDED);
  };
}

IncreaseOffer.propTypes = {
  lot: React.PropTypes.object,
  extras: React.PropTypes.object,
  shopLotActions: React.PropTypes.object,
  shopLot: React.PropTypes.object,
  yourOffer: React.PropTypes.string,
  counterOfferUnderNegotiation: React.PropTypes.string,
  increaseYourOffer: React.PropTypes.string,
  hideButton: React.PropTypes.bool
};

IncreaseOffer.defaultProps = {
  hideButton: true // <- temporary
};



