import React from 'react';
import {connect} from 'react-redux';
import {setImageSize} from 'helpers/utils';
import LinkComp from 'components/linkComp';
import _ from 'helpers/lodash';

import './expert-info.scss';


export class ExpertInfo extends React.Component {
  shouldComponentUpdate(newProps) {
    return this.props.lot.expertId !== newProps.lot.expertId ||
           this.props.lot.lotNumber !== newProps.lot.lotNumber ||
           this.props.expert.data === null ||
           this.props.expert.lang !== newProps.expert.lang ||
           this.props.extras.locale !== newProps.extras.locale;
  }

  render() {
    const lot = this.props.lot;
    const expertId = lot.expertId;
    const extras = this.props.extras;

    if (expertId) {
      const expertInfo = this.props.expert.data;
      if (!expertInfo) return null;

      const name = expertInfo.Name;
      const city = expertInfo.City;
      const expertImg = expertInfo.ThumbImage;
      const descr = expertInfo.DescriptionBulletPoints.trim();
      const lotImage = setImageSize(this.props.lotImageHeight, this.props.lotImageWidth, lot.images[0].url);
      const slug = `${name} ${city}`.replace(/\s/g, '-').toLowerCase(); // cool if we can get info from API
      const expertURL = extras.addLocaleToUrl(`/p/${expertId}/${slug}`);
      const descriptionList = descr && descr.split('<br>').map((item, index) => <li key={index}>{item.trim()}</li>);

      return (
        <div className="expert-info">
          <div className="expert-info__product-description col-12">
            <div className="md-col-9 md-col">
              <h3>{this.props.descriptionTitle}</h3>
              <p>{lot.expertRecommendationText}</p>
            </div>
            <div className="md-col-3 md-col">
              <img src={lotImage} alt={lot.title} />
            </div>
          </div>

          <div className="expert-info__expert-description">
            {expertImg && <img src={expertImg} alt={name} />}
            <div>
              <LinkComp link={expertURL} extras={extras} className="expert-info__expert-description-name"
                        target="_blank">
                {name}
              </LinkComp>
              <div className="expert-info__expert-description-city">{city}</div>
              <div className="expert-info__expert-description-bio">
                <h4>{this.props.bio}</h4>
                <ul className="expert-info__expert-description-bio-description">{descriptionList}</ul>
              </div>
            </div>
          </div>
        </div>
      );
    }

    return null;
  };

}

ExpertInfo.propTypes = {
  data: React.PropTypes.object,
  extras: React.PropTypes.object,
  lot: React.PropTypes.object,
  expert: React.PropTypes.object,
  descriptionTitle: React.PropTypes.string,
  bio: React.PropTypes.string,
  expertIn: React.PropTypes.string,
  lotImageWidth: React.PropTypes.number,
  lotImageHeight: React.PropTypes.number
};

ExpertInfo.defaultProps = {
  lotImageWidth: 620,
  lotImageHeight: 465
};

function mapStateToProps(state) {
  return {
    lot: _.get(state, 'modules.data.lot', {}),
    expert: state.expert
  };
}

export default connect(mapStateToProps)(ExpertInfo);



