import React, {PropTypes} from 'react';
import Swipeable from 'react-swipeable';
import ImageElement from 'components/images/ImageElement.jsx';
import FlowPlayerElement from 'live-stream/FlowPlayerElement';
import LinkElement from 'components/link-element/LinkElement';

import {classes, setImageSize, isBrowser, scrollIntoView} from 'helpers/utils';

import './mobile.scss';

/**
 * Padding threshold.
 * @type {number}
 */
const SCROLL_PADDING = 5;

const initState = {
  selectedIndex: 0,
  isVideoPlaying: false
};

export default class MobileGalleryComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = initState;

    // img will load based on swipe direction
    this.imgLoadingRight = false;
    this.isAllowedToSwipe = true;
  }

  /**
   * Set selected image index to state.
   * @param {number} index selected image index
   */
  setSelectedImageIndex(index) {
    this.setState({
      selectedIndex: index,
      isVideoPlaying: false
    });

    this.alignThumbContainer(index);
  }

  /**
   * Select image by index.
   * @param {number} imageIndex index
   */
  selectImage(imageIndex) {
    this.imgLoadingRight = imageIndex < this.state.selectedIndex;

    this.setSelectedImageIndex(imageIndex);
  }

  /**
   * Set selected image index based on diff.
   * @param {number} diff 1 or -1, based what we want to set next or previous
   */
  setNextIndex(diff) {
    if (this.isAllowedToSwipe) {
      const imageIndex = getNextIndex(this.state.selectedIndex + diff, this.props.images.length);

      this.imgLoadingRight = diff === -1;

      this.setSelectedImageIndex(imageIndex);
    }
  }

  /**
   * Set next image index.
   */
  nextImage = () => {
    this.setNextIndex(1);
  };

  /**
   * Set previous image index.
   */
  prevImage = () => {
    this.setNextIndex(-1);
  };

  /**
   * Set is video playing. Need to remove highlight from thumb
   * @param {boolean} isEnabled is video playing or not
   */
  setVideoPlaying(isEnabled) {
    this.setState({
      isVideoPlaying: isEnabled
    });

    this.scrollToPlayedVideo();
  }

  /**
   * Scrolls video to view, if it is not visible
   */
  scrollToPlayedVideo = () => {
    const viewContainer = this.refs.viewContainer;

    if (viewContainer) {
      scrollIntoView(viewContainer);
    }
  };

  /**
   * Sets the proper height as a proportion to width
   * @returns {Object} Style with correct height
   */
  calculateViewHeight = () => {
    if (isBrowser()) {
      return {
        height: (document.documentElement.clientWidth - 20) * 0.75 + 'px'
      };
    }
  };

  /**
   * Update is allowed to swipe. Need on mobile withing zoom, so user can scroll zoomed image and view it.
   * Swipe should work only when page is zoom and user on bound of screen.
   */
  updateIsSwipeAllowed = () => {
    if (isBrowser()) {
      this.isAllowedToSwipe =
        (window.scrollX <= SCROLL_PADDING ||
         document.body.offsetWidth - (window.innerWidth + window.scrollX) <= SCROLL_PADDING);
    }
  };

  render() {
    const {extras} = this.props;
    const viewImg = this.getImage(this.state.selectedIndex);
    const title = this.getTitle(this.state.selectedIndex);

    const mainImgView = (
      <Swipeable onSwipedRight={this.prevImage} onSwipedLeft={this.nextImage}>
        <ImageElement
          imageUrl={viewImg}
          extras={{...extras, lazyLoading: false}}
          imgLoadingRight={this.imgLoadingRight}
          title={title}
        />
      </Swipeable>
    );

    const styles = this.calculateViewHeight();

    const videoComponent = (
      <div className="mobile-gallery-component__video-container">
        <FlowPlayerElement
          className="mobile-gallery-component__video-element"
          extras={this.props.extras}
          videoUrl={this.props.video}
          config={this.props.config}
          isVideoElement
          unavailableStreamText={this.props.labels.unavailableVideoMessage}
        />
      </div>
    );

    return (
      <div className="mobile-gallery-component sm-col md-col-12">
        <div style={styles} className="mobile-gallery-component__view" ref="viewContainer">
          {this.state.isVideoPlaying ? videoComponent : mainImgView}
        </div>

        {this.renderThumbs()}
      </div>
    );
  }

  /**
   * @override
   */
  componentDidMount() {
    if (isBrowser()) {
      window.addEventListener('scroll', this.updateIsSwipeAllowed);
    }
  }

  componentWillUpdate(newProps) {
    if (newProps.extras.location.pathname !== this.props.extras.location.pathname) {
      this.setState(initState);

      this.alignThumbContainer(0);
    }
  }

  /**
   * @override
   */
  componentWillUnmount() {
    window.removeEventListener('scroll', this.updateIsSwipeAllowed);
  }

  /**
   * Display watch video label.
   */
  watchVideo() {
    if (this.props.video) {
      const on = this.setVideoPlaying.bind(this, true);

      return (
        <div className="mobile-gallery-component__watch-video">
          <LinkElement label="Watch Video" className="link link--with-icon link--video" onClick={on} />
        </div>
      );
    }
  }

  /**
   * Render thumbs.
   * @returns {Array} react component
   */
  renderThumbs() {
    if (this.props.images.length <= 1) {
      return null;
    }

    const thumbs = this.props.images.map((value, index) => {
      const selectImage = this.selectImage.bind(this, index);
      const thumbClass = classes({
        'mobile-gallery-component__img': true,
        'mobile-gallery-component__img--active': this.state.isVideoPlaying ? false : this.state.selectedIndex === index
      });

      const thumbImg = this.getImage(index, 'thumb');
      const title = this.getTitle(index);

      return (
        <div
          className={thumbClass} key={index} onClick={selectImage} ref={createThumbRef(index)}>
          <ImageElement
            imageUrl={thumbImg}
            extras={this.props.extras}
            title={title}
          />
        </div>
      );
    });

    let videoThumb;
    if (this.props.video) {
      const on = this.setVideoPlaying.bind(this, true);
      const label = this.props.labels.lotGalleryWatchVideo;

      videoThumb = (
        <div className="mobile-gallery-component__img mobile-gallery-component__img-video" onClick={on}>
          <ImageElement imageUrl={this.getImage(0, 'thumb')}
                        extras={this.props.extras} title={this.props.labels.lotGalleryWatchVideo} />
          <span>{label}</span>
        </div>
      );
    }

    return (
      <div className="mobile-gallery-component__thumbs">
        {videoThumb}
        {thumbs}
      </div>
    );
  }

  /**
   * Return appropriate image based on index and img type
   * @param {number} index image index
   * @param {string} [type=large] `thumb` or `large`
   * @returns {string}
   */
  getImage(index, type = 'large') {
    const imgType = this.props[`${type}ImageSize`];

    return setImageSize(
      imgType.height,
      imgType.width,
      this.props.images[index].url);
  }

  /**
   * Return title for particular image index.
   * @param {number} index image index
   * @returns {string} title
   */
  getTitle(index) {
    const titles = [].concat(this.props.caption, this.props.features);
    return titles[index] || this.props.caption;
  }

  /**
   * Align thumb container to current selected image.
   * @param {number} index current selected image
   */
  alignThumbContainer(index) {
    const selectedThumb = this.refs[createThumbRef(index)];
    if (selectedThumb) {
      scrollIntoView(selectedThumb);
    }
  }
}

/**
 * Calculate next array index based on income index.
 * Calculate index on cycle way, which returns index `0 > index < length`.
 * For example, if array has `length` 4. Passing `currentIndex` as 5, returns 1
 * and passing `currentIndex` as -1 return 3.
 *
 * @param {number} currentIndex index that we need
 * @param {number} length array length
 * @returns {number} appropriate index
 */
function getNextIndex(currentIndex, length) {
  return ((currentIndex % length) + length) % length;
}

/**
 * Create thumb id for refs.
 * @param {number} id any id
 * @returns {string}
 */
function createThumbRef(id) {
  return `thumb-${id}`;
}

MobileGalleryComponent.propTypes = {
  images: PropTypes.array,
  video: PropTypes.string,
  extras: PropTypes.object,
  largeImageSize: PropTypes.object,
  thumbImageSize: PropTypes.object,
  config: PropTypes.object,
  labels: PropTypes.object,
  caption: PropTypes.string,
  features: PropTypes.array
};

MobileGalleryComponent.defaultProps = {
  images: [],
  features: [],
  video: '',
  extras: {},
  caption: '',
  largeImageSize: {
    width: 1000,
    height: 750
  },
  thumbImageSize: {
    width: 100,
    height: 75
  },
  config: {},
  labels: {}
};



