import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as LotActions from 'ui/actions/LotActions';
import _ from 'helpers/lodash';
import LinkComp from 'components/linkComp';
import LinkElement from 'components/link-element/LinkElement';
import LotsNavigation from 'components/lot-navigation/LotsNavigation';
import { buildAuctionLink } from 'helpers/utils';
import GalleryComponent from 'gallery/GalleryComponent';
import PrintButton from 'components/action-button-print/PrintButton';
import EmailButton from 'components/action-button-email/EmailButton';
import ButtonElement from 'components/buttons/ButtonElement';
import PriceComp from 'components/priceComp';
import PriceTooltip from 'components/price-tooltip/PriceTooltip';
import VGBildPlaceholder from 'components/vgbild-placeholder/VGBildPlaceholder';

import './lot-result-teaser.scss';

export class LotResultTeaserComponent extends React.Component {

  shouldComponentUpdate(nextProps) {
    const nextLotNumber = _.get(nextProps, 'lot.lotNumber', -1);
    return nextLotNumber > 0;
  }

  /**
   * Show back link.
   */
  renderBackLink() {
    const {extras, auction, backToAuction} = this.props;
    let text = this.props.backToAuctionText;
    if (!extras.isMobile) {
      text = `${backToAuction} ${auction.title}`;
    }

    let linkProps = {
      spaLink: buildAuctionLink(auction, extras),
      extras: extras,
      className: 'link link--with-icon link--back'
    };

    return (
      <div className="lot-teaser__back-to-auction">
        <LinkComp {...linkProps}>
          {text}
        </LinkComp>
      </div>
    );
  }

  processLabelsByLotState(lotStatus, props) {
    const mapping = {
      Sold: {
        firstCTADescription: props.findSimilarDescSold,
        secondCTADescription: props.reqValuationDescSold,
        priceDetails: props.priceDetailsInfo,
        statusLabel: props.soldStatusText
      },
      NotSold: {
        firstCTADescription: props.findSimilarDescNotSold,
        secondCTADescription: props.reqValuationDescNotSold,
        statusLabel: props.notSoldStatusText
      },
      SoldConditionally: {
        firstCTADescription: props.findSimilarDescSoldConditionally,
        secondCTADescription: props.reqValuationDescSoldConditionally,
        priceDetails: props.priceDetailsInfoConditional,
        statusLabel: props.conditionalSaleStatusText
      },
      NotSoldReserveNotMet: {
        firstCTADescription: props.findSimilarDescNotSoldReserveNotMet,
        secondCTADescription: props.reqValuationDescNotSoldReserveNotMet,
        statusLabel: props.reserveNotMetStatusText
      }
    };

    return mapping[lotStatus] || {};
  }

  renderStatus(labelsObj, status = '') {
    const {lot, extras} = this.props;
    const priceInfoObj = status === 'Sold' || status === 'SoldConditionally' ?
      (
        <span className="price-info">
          <PriceComp value={lot.soldPrice.value} currency={lot.soldPrice.currency} extras={extras} />
          <PriceTooltip {...this.props} priceDetailsInfo={labelsObj.priceDetails} />
        </span>
      ) : null;

    return (
      <div className={`lot-result-teaser__status-${status.toLowerCase()}`}>
        <span className="status">{labelsObj.statusLabel}</span>
        {priceInfoObj}
      </div>
    );
  }

  render() {
    const {
      lot,
      extras,
      user,
      lotQuestionHoverText,
      lotQuestionLink,
      requestValuationButtonText,
      requestValuationButtonLink,
      findSimilarButtonText,
      startingPriceLabel,
      lastBidLabel,
      estimatedValueLabel
    } = this.props;

    const catalogNumber = _.get(lot, 'catalogNumber', 0);
    const title = _.get(lot, 'title', 'NO TITLE');
    const isHandheld = _.get(extras, 'isHandheld');
    const saleStatus = _.get(lot, 'status.saleStatus', '');
    const labelsObj = this.processLabelsByLotState(saleStatus, this.props);
    const startingBid = _.get(lot, 'startingBid', {});
    const lastBid = _.get(lot, 'lastBid', {});
    const estimate = _.get(lot, 'estimate', {});
    const lastBidBlock = saleStatus === 'NotSoldReserveNotMet' ? (
      <div className="lot-result-teaser__price-block">
        {lastBidLabel}
        <PriceComp value={lastBid.value} currency={lastBid.currency} extras={extras} />
      </div>
    ) : null;
    const gallery = lot.representedVgBild && saleStatus === 'Sold' ? <VGBildPlaceholder /> : <GalleryComponent {...this.props} />;
    
    return (
      <div className="section-wrapper clearfix lot-result-teaser px1">
        <div className="col-12 clearfix">
          <div className="clearfix">
            <div className="left">
              {this.renderBackLink()}
            </div>
            <div className="right">
              <LotsNavigation {...this.props} />
            </div>
          </div>
        </div>

        <div className="sm-col-12 md-col-8 md-col">
          {gallery}
        </div>

        <div className="sm-col-12 md-col-4 md-col lot-result-teaser__description_wrapper">
          <h1 className="lot-result-teaser__title">
            <span className="lot-result-teaser__lot-number">{catalogNumber} |</span> {title}
          </h1>

          <div className="lot-result-teaser__status-container">
            {this.renderStatus(labelsObj, saleStatus)}
          </div>

          <div className="lot-result-teaser__prices-container">
            {lastBidBlock}
            <div className="lot-result-teaser__price-block">
              {startingPriceLabel}
              <PriceComp value={startingBid.value} currency={startingBid.currency} extras={extras} />
            </div>
            <div className="lot-result-teaser__price-block">
              {estimatedValueLabel}
              <PriceComp value={estimate.value} currency={estimate.currency} extras={extras} />
            </div>
          </div>

          <div className="lot-result-teaser__cta-container">
            <div className="lot-result-teaser__cta-block">
              <div>
                <span>{labelsObj.firstCTADescription}</span>
              </div>

              <ButtonElement
                text={findSimilarButtonText}
                buttonType="primary"
                url={_.get(lot, 'categoryUrl', '')} />
            </div>

            <div className="lot-result-teaser__cta-block">
              <div>
                <span>{labelsObj.secondCTADescription}</span>
              </div>

              <LinkComp className="link link--with-icon lot-result-teaser__cta-link"
                        link={extras.addLocaleToUrl(requestValuationButtonLink)}
                        extras={extras}>{requestValuationButtonText}</LinkComp>
            </div>
          </div>


          <div className="lot-teaser__action-button">
            <EmailButton
              body={this.props.emailBody}
              subject={this.props.emailSubject}
              title={this.props.emailHoverTitle}
              dynamicTitle={this.props.dynamicTitle}
              userEmail={user.email}
            />
            {!isHandheld && <PrintButton title={this.props.printHoverTitle} />}
            <LinkElement link={lotQuestionLink} className="link link--action-button link--question"
                         title={lotQuestionHoverText}
            />
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    lot: _.get(state, 'modules.data.lot', {}),
    auction: _.get(state, 'modules.data.auction', {}),
    userLoggedIn: !!state.user.email,
    user: state.user,
    config: state.config,
    dynamicTitle: state.modules.module.content.dynamicTitle
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(LotActions, dispatch)
  };
}
LotResultTeaserComponent.propTypes = {
  lot: PropTypes.object,
  auction: PropTypes.object,
  extras: PropTypes.object,
  user: PropTypes.object,
  lotQuestionHoverText: PropTypes.string,
  lotQuestionLink: PropTypes.string,
  requestValuationButtonText: PropTypes.string,
  requestValuationButtonLink: PropTypes.string,
  findSimilarButtonText: PropTypes.string,
  backToAuctionText: PropTypes.string,
  startingPriceLabel: PropTypes.string,
  emailBody: PropTypes.string,
  emailSubject: PropTypes.string,
  emailHoverTitle: PropTypes.string,
  dynamicTitle: PropTypes.string,
  printHoverTitle: PropTypes.string,
  backToAuction: PropTypes.string,
  lastBidLabel: PropTypes.string,
  estimatedValueLabel: PropTypes.string
};

export default connect(mapStateToProps, mapDispatchToProps)(LotResultTeaserComponent);



