import React, {PropTypes} from 'react';
import LightboxComponent from './LightboxComponent';
import ImageElement from 'components/images/ImageElement';
import {setImageSize, classes} from 'helpers/utils';
import FlowPlayerElement from 'live-stream/FlowPlayerElement';
import LinkElement from 'components/link-element/LinkElement';

import './desktop.scss';


const initState = {
  selectedItem: 0,
  expanded: false,
  isLightboxDisplayed: false,
  isVideoPlaying: false
};

export default class DesktopGalleryComponent extends React.Component {
  constructor() {
    super();

    this.state = initState;
  }

  /**
   * Render the component
   * @returns {XML}
   */
  render() {
    const {thumbnailCapacity, labels, video} = this.props;
    let sliceEnd = this.state.expanded ? (this.props.images.length) : thumbnailCapacity;
    if (video && !this.state.expanded) --sliceEnd;

    let images = this.props.images.slice(0, sliceEnd);

    return (
      <div className="lot-gallery-component__main-container">
        {this.renderViewContainer()}

        <div className="lot-gallery-component__bar-container clearfix">
          <div className="sm-col sm-col-5 md-col-5">
            <div className="lot-gallery-component__enlarge-image">
              <LinkElement label={labels.lotGalleryEnlargeImage}
                           className="link link--with-icon link--enlarge link--cut-text"
                           onClick={this.toggleLightbox} />
            </div>
          </div>
          <div className="sm-col-right md-col-right lot-gallery-component__counter">
            <span>{this.state.selectedItem + 1}</span>
            <span> / </span>
            <span>{this.props.images.length}</span>
          </div>
        </div>
        {this.renderThumbnails(images, video)}
        {this.renderViewMoreButton()}
        {this.renderLightbox(this.props.images)}
      </div>
    );
  }

  componentWillUpdate(newProps) {
    if (newProps.extras.location.pathname !== this.props.extras.location.pathname) {
      this.setState(initState);
    }
  }

  /**
   * Handles selection of image from thumbs panel
   * @param {Object} item Currently selected image
   */
  handleItemSelection = (item) => {
    this.setState(
      {
        selectedItem: item.index,
        isVideoPlaying: false
      }
    );
  };

  /**
   * Toggles View More/Less button
   */
  toggleViewMore = () => {
    this.setState({
      expanded: !this.state.expanded
    });
  };

  /**
   * Toggling state of lightbox
   */
  toggleLightbox = () => {
    this.setState({
      isLightboxDisplayed: !this.state.isLightboxDisplayed
    });
  };

  onLightboxClosed = () => {
    this.toggleLightbox();
  };

  /**
   * Lightbox rendering with appropriate params
   * @param {Array} images List of images which will be used in lightbox
   * @returns {Object} Node tree with rendered component
   */
  renderLightbox = (images) => {
    if (this.state.isLightboxDisplayed) {
      return <LightboxComponent images={images} extras={{
        onLightboxClosed: this.onLightboxClosed,
        selectedItem: this.state.selectedItem,
        caption: this.props.caption
      }} />;
    }
  };

  /**
   * Renders View More/Less button
   * @returns {Object} Node tree with rendered component
   */
  renderViewMoreButton = () => {
    if (this.props.images.length <= this.props.thumbnailCapacity) {
      return null;
    }

    const labelText = this.state.expanded ? this.props.labels.lotGalleryViewLess : this.props.labels.lotGalleryViewMore;

    return (
      <div className="lot-gallery-component__view-more-wrapper sm-col sm-col-12 md-col-12">
        <div className="lot-gallery-component__view-more col-right">
          <span onClick={this.toggleViewMore}>{labelText}</span>
        </div>
      </div>
    );
  };

  /**
   * Set video playing flag to true/false in state
   * @param {Boolean} isEnabled flag which is needed to be set in state
   */
  setVideoPlaying = (isEnabled) => {
    this.setState({
      isVideoPlaying: isEnabled
    });
  };

  /**
   * Toggles playing video
   */
  toggleVideoPlaying = () => {
    return this.setVideoPlaying(!this.state.isVideoPlaying);
  };

  /**
   * Renders an appropriate view container for image or video
   * @returns {Object} Node with corresponding component
   */
  renderViewContainer = () => {
    const {mainImageViewHeight, mainImageViewWidth, extras, video, config, labels} = this.props;
    const title = this.getTitle(this.state.selectedItem);

    if (!this.state.isVideoPlaying && this.props.images[this.state.selectedItem]) {
      return (
        <div className="lot-gallery-component__view-container" ref="viewContainer" onClick={this.toggleLightbox}>
          <ImageElement
            imageUrl={setImageSize(mainImageViewHeight, mainImageViewWidth,
              this.props.images[this.state.selectedItem].url)}
            extras={{...extras, lazyLoading: false}} title={title} />
        </div>
      );
    }

    return (
      <div className="lot-gallery-component__view-container" ref="viewContainer">
        <div className="lot-gallery-component__view-container--video">
          <FlowPlayerElement
            className="lot-gallery-component__video-element"
            extras={extras}
            videoUrl={video}
            config={config}
            unavailableStreamText={labels.unavailableVideoMessage}
            isVideoElement
          />
        </div>
      </div>
    );
  };

  /**
   * Renders thumbnails
   * @param {Array} images Array of images which are used in thumbs panel
   * @param {String} video video URL
   * @returns {Object} Node tree with rendered component
   */
  renderThumbnails = (images, video) => {
    if (images.length <= 1) {
      return null;
    }

    const {thumbnailImageHeight, thumbnailImageWidth} = this.props;
    const thumbList = images.map((item, index) => {
      const title = this.getTitle(index);
      const thumbClass = classes({
        'lot-gallery-component__item--selected': (index === this.state.selectedItem) && !this.state.isVideoPlaying,
        'lot-gallery-component__item': true
      });

      return (
        <div key={index} className="lot-gallery-component__item-container">
          <div className={thumbClass} onClick={() => this.handleItemSelection({item, index})}>
            <ImageElement imageUrl={setImageSize(thumbnailImageHeight, thumbnailImageWidth, item.url)}
                          extras={this.props.extras} title={title} />
          </div>
        </div>
      );
    });

    let videoThumb;
    if (video) {
      const label = this.props.labels.lotGalleryWatchVideo;
      videoThumb = (
        <div className="lot-gallery-component__item-container lot-gallery-component__item-container-video" onClick={this.toggleVideoPlaying}>
          <ImageElement imageUrl={setImageSize(thumbnailImageHeight, thumbnailImageWidth, images[0].url)}
                        extras={this.props.extras} title={label} />
          <span>{label}</span>
        </div>
      );
    }

    return (
      <div className="lot-gallery-component__items-container clearfix">
        {videoThumb}
        {thumbList}
      </div>
    );
  };

  /**
   * Return title for particular image index.
   * @param {number} index image index
   * @returns {string} title
   */
  getTitle(index) {
    const titles = [].concat(this.props.caption, this.props.features);
    return titles[index] || this.props.caption;
  }

}

DesktopGalleryComponent.propTypes = {
  images: PropTypes.array,
  extras: PropTypes.object,
  caption: PropTypes.string,
  video: PropTypes.string,
  labels: PropTypes.object,
  config: PropTypes.object,
  thumbnailCapacity: PropTypes.number,
  mainImageViewHeight: PropTypes.number,
  mainImageViewWidth: PropTypes.number,
  thumbnailImageHeight: PropTypes.number,
  thumbnailImageWidth: PropTypes.number,
  features: PropTypes.array
};

DesktopGalleryComponent.defaultProps = {
  images: [],
  features: [],
  extras: {},
  caption: '',
  video: '',
  config: {},
  labels: {},
  thumbnailCapacity: 4,
  mainImageViewHeight: 465,
  mainImageViewWidth: 620,
  thumbnailImageHeight: 105,
  thumbnailImageWidth: 140
};



